import { useCallback } from 'react'
import type { FlatListProps, ListRenderItem, ListRenderItemInfo } from 'react-native'
import { Platform, View } from 'react-native'
import { FlatList } from 'react-native-gesture-handler'

import StoresListItem from './StoresListItem'
import type { Store } from '../../api/types'
import { SpacingBase } from '../../constants'
import { useAppSelector } from '../../redux'
import { useStyles } from '../../utils'
import { StoresListAd } from '../Ads'
import { StoreCardPlaceholder } from '../StoreCard'

export interface StoresListProps {
  stores: Store[]
  contentContainerStyle?: FlatListProps<Store>['contentContainerStyle']
  vertical?: boolean
  showAds?: boolean
}

const ListEmptyComponent = ({ style, flat }: { style: object; flat?: boolean }) => {
  return Array.from({
    length: 20,
  }).map((_, index) => (
    <View key={index} style={style}>
      <StoreCardPlaceholder />
    </View>
  ))
}

export const StoresList = ({
  stores,
  contentContainerStyle,
  vertical,
  showAds,
}: StoresListProps) => {
  const countryCode = useAppSelector((state) => state.app.countryCode)

  const styles = useStyles(
    () => ({
      storesList: {
        paddingLeft: vertical ? SpacingBase * 2 : 0,
        paddingRight: vertical ? SpacingBase * 2 : 0,
        paddingTop: vertical ? SpacingBase : 0, // for the item shadow
        paddingBottom: vertical ? 0 : SpacingBase, // for the item shadow
      },
      storesItem: {
        marginRight: vertical ? 0 : SpacingBase * 2,
        marginBottom: vertical ? SpacingBase * 2 : 0,
        width: vertical ? 'auto' : 304,
      },
    }),
    [vertical],
  )

  const handleRenderItem: ListRenderItem<Store> = useCallback(
    ({ item, index }: ListRenderItemInfo<Store>) => {
      return (
        <View style={styles.storesItem}>
          <StoresListItem countryCode={countryCode} store={item} />
        </View>
      )
    },
    [countryCode, styles.storesItem],
  )

  return (
    <FlatList
      keyExtractor={(item) => item.id!}
      data={stores}
      renderItem={handleRenderItem}
      horizontal={!vertical}
      ListEmptyComponent={<ListEmptyComponent style={styles.storesItem} />}
      contentContainerStyle={[styles.storesList, contentContainerStyle]}
      ItemSeparatorComponent={Platform.OS !== 'web' && showAds ? StoresListAd : undefined}
    />
  )
}

export default StoresList
