import { useLinkProps } from '@react-navigation/native'
import { useCallback } from 'react'
import { Platform } from 'react-native'
import { Drawer } from 'react-native-paper'

import { Copies } from '../constants'
import { setTheme, showPrivacyOptionsForm, useAppDispatch, useAppSelector } from '../redux'

const MainDrawer = () => {
  const countryCode = useAppSelector((state) => state.app.countryCode)
  const theme = useAppSelector((state) => state.ui.theme)
  const dispatch = useAppDispatch()

  const { onPress: onContactsPress } = useLinkProps({
    to: { screen: 'Contacts', params: { countryCode } },
  })

  const handleOnSwitchTheme = useCallback(() => {
    dispatch(setTheme(theme === 'dark' ? 'light' : 'dark'))
  }, [dispatch, theme])

  const handleOnConsentForm = useCallback(async () => {
    await dispatch(showPrivacyOptionsForm())
  }, [dispatch])

  return (
    <Drawer.Section showDivider={false}>
      <Drawer.Item
        label={Copies.toggle_theme}
        icon="theme-light-dark"
        onPress={handleOnSwitchTheme}
      />
      <Drawer.Item
        label={Copies.contacts}
        icon="message-reply-text-outline"
        onPress={onContactsPress}
      />
      {Platform.OS !== 'web' && (
        <Drawer.Item label={Copies.consent_form} icon="eye-outline" onPress={handleOnConsentForm} />
      )}
    </Drawer.Section>
  )
}

export default MainDrawer
