import { Helmet } from 'react-helmet'
import { Platform } from 'react-native'

export interface HeadPreloadImgProps {
  imgUrl: string
  imgType?: string | undefined
}

const HeadPreloadImg = ({ imgUrl, imgType }: HeadPreloadImgProps) => {
  if (Platform.OS !== 'web') {
    return null
  }

  return (
    // TODO: see if Helmet is really needed or we got other options
    <Helmet>
      <link
        rel="preload"
        // eslint-disable-next-line react/no-unknown-property
        fetchPriority="high"
        as="image"
        href={imgUrl}
        type={imgType}
      />
    </Helmet>
  )
}

export default HeadPreloadImg
