import { useCallback } from 'react'
import type { FlatListProps, ListRenderItem, ListRenderItemInfo } from 'react-native'
import { StyleSheet, FlatList, View } from 'react-native'

import CataloguesListItem from './CataloguesListItem'
import type { Catalogue } from '../../api/types'
import { SpacingBase } from '../../constants'
import { useAppSelector } from '../../redux'
import { CatalogueCardPlaceholder } from '../CatalogueCard'

export interface CataloguesListProps {
  catalogues: Catalogue[]
  contentContainerStyle?: FlatListProps<Catalogue>['contentContainerStyle']
}

const styles = StyleSheet.create({
  contentContainer: {
    paddingBottom: SpacingBase * 2,
  },
  item: {
    width: 155.5 - SpacingBase, // width of an item in two columns grid - SpacingBase to see the third item
    marginRight: SpacingBase * 2,
  },
})

const ListEmptyComponent = () => {
  return Array.from({
    length: 10,
  }).map((_, index) => (
    <View key={index} style={styles.item}>
      <CatalogueCardPlaceholder />
    </View>
  ))
}

export const CataloguesList = ({ catalogues, contentContainerStyle }: CataloguesListProps) => {
  const countryCode = useAppSelector((state) => state.app.countryCode)

  const handleRenderItem: ListRenderItem<Catalogue> = useCallback(
    ({ item, index }: ListRenderItemInfo<Catalogue>) => {
      return (
        <View style={styles.item}>
          <CataloguesListItem item={item} index={index} countryCode={countryCode} />
        </View>
      )
    },
    [countryCode],
  )

  return (
    <FlatList
      horizontal
      data={catalogues}
      renderItem={handleRenderItem}
      ListEmptyComponent={ListEmptyComponent}
      keyExtractor={(item) => item.id!}
      contentContainerStyle={[styles.contentContainer, contentContainerStyle]}
    />
  )
}

export default CataloguesList
