import { A } from '@expo/html-elements'
import type { StackScreenProps } from '@react-navigation/stack'
import { Image } from 'expo-image'
import { useCallback, useEffect, useMemo, useState } from 'react'
import { ErrorBoundary } from 'react-error-boundary'
import { StyleSheet, View } from 'react-native'
import { ActivityIndicator, Appbar, Text, useTheme } from 'react-native-paper'

import { useGetCataloguesByIdsQuery, useGetNearbyStoresByCatalogueIdQuery } from '../api'
import type { Catalogue } from '../api/types'
import { BottomSheet, CatalogueDetailSheet } from '../components/BottomSheets'
import CatalogueStoreDialog from '../components/CatalogueStoreDialog'
import ErrorFallback from '../components/ErrorFallback'
import type { HeaderProps } from '../components/Header'
import Header from '../components/Header'
import { BottomSheetHandleHeight, Copies, ExtraColors, SpacingBase } from '../constants'
import type { RootStackParamList } from '../navigation/types'
import {
  deleteFavouriteCatalogue,
  setFavouriteCatalogue,
  useAppDispatch,
  useAppSelector,
} from '../redux'

export type CatalogueDetailScreenProps = StackScreenProps<
  RootStackParamList,
  'CatalogueDetail',
  'RootStack'
>

const styles = StyleSheet.create({
  main: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  cover: {
    width: '100%',
    height: 350,
    marginBottom: SpacingBase * 2,
  },
  badge: {
    marginTop: SpacingBase * 2,
    width: 215.33,
    height: 83.33,
  },
})

export const CatalogueDetailScreen = ({ route, navigation }: CatalogueDetailScreenProps) => {
  const [catalogue, setCatalogue] = useState<Catalogue | null>(null)
  const { id } = route.params
  const { location, favourite_catalogues } = useAppSelector((state) => state.user)
  const isFav = useMemo(
    () => (catalogue?.id ? favourite_catalogues.includes(catalogue.id) : false),
    [catalogue, favourite_catalogues],
  )
  const dispatch = useAppDispatch()
  const { colors } = useTheme()

  const { data: catalogues, error, isLoading } = useGetCataloguesByIdsQuery([id])

  const { data: stores = [], error: storesError } = useGetNearbyStoresByCatalogueIdQuery(
    {
      id,
      // @ts-expect-error: skip
      latitude: location?.latitude,
      // @ts-expect-error: skip
      longitude: location?.longitude,
    },
    { skip: !(catalogue && location) },
  )

  if (error || storesError) {
    throw new Error(JSON.stringify(error || storesError))
  }

  const handleOnPressFav = useCallback(async () => {
    if (!catalogue) {
      return
    }
    if (isFav) {
      dispatch(deleteFavouriteCatalogue(catalogue.id!))
    } else {
      await dispatch(setFavouriteCatalogue(catalogue.id!))
    }
  }, [catalogue, dispatch, isFav])

  const renderHeader = useCallback(
    (props: HeaderProps) => {
      return (
        // eslint-disable-next-line react/jsx-props-no-spreading
        <Header {...props}>
          <Appbar.Action
            size={SpacingBase * 4}
            onPress={handleOnPressFav}
            color={ExtraColors.gold}
            icon={isFav ? 'star' : 'star-outline'}
          />
        </Header>
      )
    },
    [handleOnPressFav, isFav],
  )

  useEffect(() => {
    if (catalogues) {
      setCatalogue(catalogues[0])
      navigation.setOptions({
        header: renderHeader,
        headerTitle: `${Copies.catalogue} ${catalogues[0]._retailer_name} - ${catalogues[0].title}`,
        title: `${process.env.EXPO_PUBLIC_APP_NAME} - ${Copies.catalogue} ${catalogues[0]._retailer_name}, ${catalogues[0].title}`,
      })
    }
  }, [catalogues, navigation, renderHeader])

  return (
    <ErrorBoundary FallbackComponent={ErrorFallback}>
      {isLoading || !catalogue ? (
        <View style={styles.main}>
          <ActivityIndicator color={colors.secondaryContainer} size="large" />
        </View>
      ) : (
        <View style={styles.main}>
          <Image source={catalogue.thumb_url} contentFit="contain" style={styles.cover} />
          <Text variant="bodyLarge">{Copies.download_app_to_see_catalogue_1}</Text>
          <A href={process.env.EXPO_PUBLIC_GOOGLE_PLAY_URL} target="_blank">
            <Text variant="bodyLarge">{Copies.download_app_to_see_catalogue_2}</Text>
          </A>
          <A href={process.env.EXPO_PUBLIC_GOOGLE_PLAY_URL} target="_blank">
            <Image
              // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
              source={require('../assets/images/google-play-badge.png')}
              contentFit="contain"
              style={styles.badge}
            />
          </A>
        </View>
      )}
      <BottomSheet
        backgroundColor={colors.secondaryContainer}
        snapPoints={
          stores.length > 0
            ? [BottomSheetHandleHeight * 1.5, 380]
            : [BottomSheetHandleHeight * 1.5, 200]
        }
        handleComponentText={Copies.stores_and_settings}
      >
        <CatalogueDetailSheet
          stores={stores}
          contentColor={colors.onSecondaryContainer}
          onFavourite={handleOnPressFav}
          isFavourite={isFav}
        />
      </BottomSheet>
      <CatalogueStoreDialog />
    </ErrorBoundary>
  )
}

export default CatalogueDetailScreen
