import type { PayloadAction } from '@reduxjs/toolkit'
import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'

import type { RootState } from './store'
import type { Location } from './types'
import { DefaultLocations, FiltersDefault } from '../constants'
import type { CountryCode } from '../navigation/types'
import { logEvent, setUserProperties, storage, storageKeys } from '../utils'

export interface UserState {
  onboard: boolean
  location: Location | null
  favourite_catalogues: string[]
  favourite_stores: string[]
  filters: {
    distance: number
    retailer_ids: string[]
  }
  catalogues_view_count: number
}

const getInitialCatalogueViewCount = () => {
  const storedCount = storage.getNumber(storageKeys.catalogues_view_count)
  return storedCount ?? 0
}

const getInitialFavCatalogues = () => {
  const storedFavs = storage.getString(storageKeys.favourite_catalogues)
  if (storedFavs) {
    return JSON.parse(storedFavs) as string[]
  } else {
    return []
  }
}

const getInitialFavStores = () => {
  const storedFavs = storage.getString(storageKeys.favourite_stores)
  if (storedFavs) {
    return JSON.parse(storedFavs) as string[]
  } else {
    return []
  }
}

const getInitialCataloguesFilters = () => {
  const filters = storage.getString(storageKeys.filters)
  if (filters) {
    return JSON.parse(filters) as UserState['filters']
  } else {
    return FiltersDefault
  }
}

const getInitialLocation = () => {
  const storedLocation = storage.getString(storageKeys.location)
  if (storedLocation) {
    return JSON.parse(storedLocation) as Location
  } else {
    return null
  }
}

const initialState: UserState = {
  onboard: storage.getBoolean(storageKeys.onboard) ?? false,
  location: getInitialLocation(),
  favourite_catalogues: getInitialFavCatalogues(),
  favourite_stores: getInitialFavStores(),
  filters: getInitialCataloguesFilters(),
  catalogues_view_count: getInitialCatalogueViewCount(),
}

export const setDefaultLocation = createAsyncThunk<
  UserState['location'],
  void,
  {
    state: RootState
  }
>('user/setDefaultLocation', (_, { dispatch, getState }) => {
  const state = getState()
  const defaultLocation = DefaultLocations[state.app.countryCode]
  dispatch(deleteFilters())
  return defaultLocation
})

export const setLocation = createAsyncThunk('user/setLocation', (value: Location, { dispatch }) => {
  storage.set(storageKeys.location, JSON.stringify(value))
  dispatch(deleteFilters())
  return value
})

export const setCityLocation = createAsyncThunk(
  'user/setCityLocation',
  (value: Location, { dispatch }) => {
    storage.delete(storageKeys.location)
    dispatch(deleteFilters())
    return value
  },
)

export const setFavouriteCatalogue = createAsyncThunk<
  UserState['favourite_catalogues'],
  string,
  {
    state: RootState
  }
>('user/setFavouriteCatalogue', async (id, { getState }) => {
  const state = getState()
  await logEvent('favourite_catalogue_set', { countryCode: state.app.countryCode })
  const storedFavs = storage.getString(storageKeys.favourite_catalogues)
  const favs = storedFavs ? (JSON.parse(storedFavs) as string[]) : []
  favs.push(id)
  storage.set(storageKeys.favourite_catalogues, JSON.stringify(favs))
  return favs
})

export const setFavouriteStore = createAsyncThunk<
  UserState['favourite_stores'],
  string,
  {
    state: RootState
  }
>('user/setFavouriteStore', async (id, { getState }) => {
  const state = getState()
  await logEvent('favourite_store_set', { countryCode: state.app.countryCode })
  const storedFavs = storage.getString(storageKeys.favourite_stores)
  const favs = storedFavs ? (JSON.parse(storedFavs) as string[]) : []
  favs.push(id)
  storage.set(storageKeys.favourite_stores, JSON.stringify(favs))
  return favs
})

export const setCataloguesViewCount = createAsyncThunk<
  UserState['catalogues_view_count'],
  unknown,
  {
    state: RootState
  }
>('user/setCataloguesViewCount', async (_, { getState }) => {
  const state = getState()
  const count = state.user.catalogues_view_count + 1
  await setUserProperties({ catalogues_view_count: `${count}` })
  storage.set(storageKeys.catalogues_view_count, count)
  return count
})

export const userSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    setDefaultLocation: (state, action: PayloadAction<CountryCode>) => {
      const { latitude, longitude, display } = DefaultLocations[action.payload]
      storage.set(storageKeys.location, JSON.stringify({ latitude, longitude, display }))
      state.location = { latitude, longitude, display }
    },
    setOnboard: (state, action: PayloadAction<UserState['onboard']>) => {
      storage.set(storageKeys.onboard, action.payload)
      state.onboard = action.payload
    },
    deleteFavouriteCatalogue: (state, action: PayloadAction<string>) => {
      const storedFavs = storage.getString(storageKeys.favourite_catalogues)
      const favs = storedFavs ? (JSON.parse(storedFavs) as string[]) : []
      const newFavs = favs.filter((fav) => fav !== action.payload)
      storage.set(storageKeys.favourite_catalogues, JSON.stringify(newFavs))
      state.favourite_catalogues = newFavs
    },
    deleteFavouriteStore: (state, action: PayloadAction<string>) => {
      const storedFavs = storage.getString(storageKeys.favourite_stores)
      const favs = storedFavs ? (JSON.parse(storedFavs) as string[]) : []
      const newFavs = favs.filter((fav) => fav !== action.payload)
      storage.set(storageKeys.favourite_stores, JSON.stringify(newFavs))
      state.favourite_stores = newFavs
    },
    emptyFavouriteCatalogues: (state) => {
      storage.set(storageKeys.favourite_catalogues, JSON.stringify([]))
      state.favourite_catalogues = []
    },
    emptyFavouriteStores: (state) => {
      storage.set(storageKeys.favourite_stores, JSON.stringify([]))
      state.favourite_stores = []
    },
    setFilters: (state, action: PayloadAction<UserState['filters']>) => {
      storage.set(storageKeys.filters, JSON.stringify(action.payload))
      state.filters = action.payload
    },
    deleteFilters: (state) => {
      storage.delete(storageKeys.filters)
      state.filters = FiltersDefault
    },
  },
  extraReducers: (builder) => {
    builder.addCase(setLocation.fulfilled, (state, action) => {
      state.location = action.payload
    })
    builder.addCase(setCityLocation.fulfilled, (state, action) => {
      state.location = action.payload
    })
    builder.addCase(setDefaultLocation.fulfilled, (state, action) => {
      state.location = action.payload
    })
    builder.addCase(setCataloguesViewCount.fulfilled, (state, action) => {
      state.catalogues_view_count = action.payload
    })
    builder.addCase(setFavouriteCatalogue.fulfilled, (state, action) => {
      state.favourite_catalogues = action.payload
    })
    builder.addCase(setFavouriteStore.fulfilled, (state, action) => {
      state.favourite_stores = action.payload
    })
  },
})

export const {
  setOnboard,
  deleteFavouriteCatalogue,
  deleteFavouriteStore,
  emptyFavouriteCatalogues,
  emptyFavouriteStores,
  setFilters,
  deleteFilters,
} = userSlice.actions
export default userSlice
