import { Image } from 'expo-image'
import type { FallbackProps } from 'react-error-boundary'
import { StyleSheet, View } from 'react-native'
import { Appbar, Button, Text, ThemeProvider } from 'react-native-paper'
import { SafeAreaProvider, SafeAreaView } from 'react-native-safe-area-context'

import { Copies, SpacingBase } from '../constants'
import { LightTheme } from '../utils'

export interface ErrorFallbackProps extends FallbackProps {
  error: { message: object | string }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
  },
  main: {
    padding: SpacingBase * 4,
    gap: SpacingBase * 4,
  },
  image: {
    height: 40,
    width: 153,
  },
  text: {
    textAlign: 'center',
  },
})

// TODO: add "error" to props and send it to an error logging service
const ErrorFallback = ({ resetErrorBoundary }: ErrorFallbackProps) => {
  return (
    <SafeAreaProvider>
      <ThemeProvider theme={LightTheme}>
        <Appbar.Header mode="center-aligned">
          <Appbar.Content
            title={
              <Image
                priority="high"
                style={styles.image}
                contentFit="contain"
                alt={process.env.EXPO_PUBLIC_APP_NAME}
                // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
                source={require('../assets/images/logo-2.png')}
              />
            }
          />
        </Appbar.Header>
        <SafeAreaView style={styles.container}>
          <View style={styles.main}>
            <Text style={styles.text} variant="titleLarge">
              {Copies.something_went_wrong}
            </Text>
            <Text style={styles.text} variant="bodyLarge">
              {Copies.generic_error_text}
            </Text>
            <Button mode="contained-tonal" onPress={() => resetErrorBoundary()}>
              {Copies.retry}
            </Button>
          </View>
        </SafeAreaView>
      </ThemeProvider>
    </SafeAreaProvider>
  )
}

export default ErrorFallback
