import { configureStore } from '@reduxjs/toolkit'
// import { setupListeners } from '@reduxjs/toolkit/query'
import type { TypedUseSelectorHook } from 'react-redux'
import { useDispatch, useSelector } from 'react-redux'

import appSlice from './appSlice'
import consentSlice from './consentSlice'
import uiSlice from './uiSlice'
import userSlice from './userSlice'
import { fakeApi, supabaseApi, nominatimApi, ipapiApi, photonApi } from '../api'

export const store = configureStore({
  reducer: {
    // Add the generated reducer as a specific top-level slice
    [supabaseApi.reducerPath]: supabaseApi.reducer,
    [fakeApi.reducerPath]: fakeApi.reducer,
    [nominatimApi.reducerPath]: nominatimApi.reducer,
    [photonApi.reducerPath]: photonApi.reducer,
    [ipapiApi.reducerPath]: ipapiApi.reducer,
    app: appSlice.reducer,
    consent: consentSlice.reducer,
    ui: uiSlice.reducer,
    user: userSlice.reducer,
  },
  // Adding the api middleware enables caching, invalidation, polling,
  // and other useful features of `rtk-query`.
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware().concat(
      supabaseApi.middleware,
      fakeApi.middleware,
      nominatimApi.middleware,
      photonApi.middleware,
      ipapiApi.middleware,
    ),
})

// optional, but required for refetchOnFocus/refetchOnReconnect behaviors
// see `setupListeners` docs - takes an optional callback as the 2nd arg for customization
// setupListeners(store.dispatch)

// Infer the `RootState` and `AppDispatch` types from the store itself
export type RootState = ReturnType<typeof store.getState>
// Inferred type: {posts: PostsState, comments: CommentsState, users: UsersState}
export type AppDispatch = typeof store.dispatch

// Use throughout your app instead of plain `useDispatch` and `useSelector`
export const useAppDispatch: () => AppDispatch = useDispatch
export const useAppSelector: TypedUseSelectorHook<RootState> = useSelector
