import type { PayloadAction } from '@reduxjs/toolkit'
import { createSlice } from '@reduxjs/toolkit'
import type { ColorSchemeName } from 'react-native'

import { storage, storageKeys } from '../utils'

export interface UiState {
  catalogueStoreDialog: {
    type: 'catalogue' | 'store' | null
    open: boolean
    id: string | null
  }
  theme: ColorSchemeName
}

const initialState: UiState = {
  catalogueStoreDialog: {
    type: null,
    open: false,
    id: null,
  },
  theme: (storage.getString(storageKeys.theme) as ColorSchemeName) ?? 'light',
}

export const uiSlice = createSlice({
  name: 'ui',
  initialState,
  reducers: {
    openCatalogueStoreDialog: (
      state,
      action: PayloadAction<{ type: 'store' | 'catalogue'; id: string }>,
    ) => {
      state.catalogueStoreDialog = {
        open: true,
        id: action.payload.id,
        type: action.payload.type,
      }
    },
    closeCatalogueStoreDialog: (state) => {
      state.catalogueStoreDialog = initialState.catalogueStoreDialog
    },
    setTheme: (state, action: PayloadAction<'light' | 'dark'>) => {
      storage.set(storageKeys.theme, action.payload)
      state.theme = action.payload
    },
  },
})

export const { openCatalogueStoreDialog, closeCatalogueStoreDialog, setTheme } = uiSlice.actions

export default uiSlice
