import type { BottomTabHeaderProps } from '@react-navigation/bottom-tabs'
import { useLinkProps } from '@react-navigation/native'
import { Image } from 'expo-image'
import { StyleSheet, TouchableOpacity } from 'react-native'
import type { AppbarHeaderProps } from 'react-native-paper'
import { Appbar } from 'react-native-paper'

import { SpacingBase } from '../constants'
import { useAppSelector } from '../redux'

export interface HeaderTabProps extends BottomTabHeaderProps {
  left?: React.ReactNode
  right?: React.ReactNode
  mode?: AppbarHeaderProps['mode']
  onPressMenu?: () => void
}

const styles = StyleSheet.create({
  image: {
    height: 40,
    width: 153,
  },
})

const HeaderTab = ({ route, options, left, right, mode, onPressMenu }: HeaderTabProps) => {
  const { countryCode, citySlug } = useAppSelector((store) => store.app)
  const { onPress } = useLinkProps({
    to: {
      screen: 'HomeTabs',
      params: {
        screen: 'Catalogues',
        params: citySlug ? { countryCode, citySlug } : { countryCode },
      },
    },
  })

  return (
    <Appbar.Header mode={mode ?? 'center-aligned'}>
      <Appbar.Action size={SpacingBase * 3} icon="menu" onPress={onPressMenu} />
      <Appbar.Content
        title={
          <TouchableOpacity onPress={() => onPress()}>
            <Image
              priority="high"
              style={styles.image}
              contentFit="contain"
              alt={process.env.EXPO_PUBLIC_APP_NAME}
              // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
              source={require('../assets/images/logo-2.png')}
            />
          </TouchableOpacity>
        }
      />
      {right}
    </Appbar.Header>
  )
}

export default HeaderTab
