import { useLinkProps } from '@react-navigation/native'
import { memo, useCallback } from 'react'

import type { Store } from '../../api/types'
import type { CountryCode } from '../../navigation/types'
import { openCatalogueStoreDialog, useAppDispatch } from '../../redux'
import { StoreCard } from '../StoreCard'

export interface StoresListItemProps {
  store: Store
  countryCode: CountryCode
}

export const StoresListItem = memo(
  ({ store, countryCode }: StoresListItemProps) => {
    const dispatch = useAppDispatch()
    const { onPress } = useLinkProps({
      to: { screen: 'StoreDetail', params: { countryCode, id: store.id! } },
    })

    const handleOnLongPress = useCallback(() => {
      dispatch(openCatalogueStoreDialog({ id: store.id!, type: 'store' }))
    }, [dispatch, store.id])

    return (
      <StoreCard
        id={store.id}
        address={store.address}
        city={store.city}
        retailer={store.retailer_name}
        distance={store._distance}
        imgSource={store.retailer_thumb_url}
        onPress={() => onPress()}
        onLongPress={handleOnLongPress}
        isOpen={store._isOpen}
      />
    )
  },
  (prevProps, nextProps) => {
    return prevProps.store.id === nextProps.store.id
  },
)

export default StoresListItem
