import { View } from 'react-native'
import { ActivityIndicator, Card, useTheme } from 'react-native-paper'

import { useStyles } from '../../utils'

export const CatalogueCardPlaceholder = () => {
  const { colors } = useTheme()
  const styles = useStyles(
    ({ colors, roundness }) => ({
      main: {
        height: 305,
      },
      cover: {
        justifyContent: 'center',
        height: 195,
        backgroundColor: colors.secondaryContainer,
        borderRadius: roundness * 3,
      },
    }),
    [],
  )

  return (
    <Card style={styles.main}>
      <View style={styles.cover}>
        <ActivityIndicator color={colors.onSecondaryContainer} size="large" />
      </View>
    </Card>
  )
}

export default CatalogueCardPlaceholder
