import Slider from '@react-native-community/slider'
import { useCallback, useEffect, useMemo, useState } from 'react'
import { ScrollView, View, useWindowDimensions } from 'react-native'
import { Button, IconButton, Modal, Portal, Text, useTheme } from 'react-native-paper'
import { useSafeAreaInsets } from 'react-native-safe-area-context'

import { RetailersFilterItem } from './RetailersFilter'
import { useGetNearbyRetailersQuery } from '../api'
import type { Retailer, RetailerFilter } from '../api/types'
import { Copies, FiltersDefault, SpacingBase } from '../constants'
import { useAppSelector } from '../redux'
import { useStyles } from '../utils'

export interface FiltersModalProps {
  visible: boolean
  onDismiss: () => void
  onApply: ({ retailer_ids, distance }: { retailer_ids: string[]; distance: number }) => void
  onDelete: () => void
}

const FiltersModal = ({ visible, onDismiss, onApply, onDelete }: FiltersModalProps) => {
  const { location, filters } = useAppSelector((state) => state.user)
  const { width: windowWidth, height: windowHeight } = useWindowDimensions()
  const insets = useSafeAreaInsets()
  const { colors } = useTheme()

  const getStepFromDistance = useCallback((distance: number) => {
    switch (distance) {
      case 5000:
        return 0
      case 10000:
        return 1
      case 20000:
        return 2
      case 30000:
        return 3
      case 50000:
        return 4
      default:
        return 3
    }
  }, [])

  const [filteredRetailers, setFilteredRetailers] = useState(filters.retailer_ids)
  const [sliderStep, setSliderStep] = useState(getStepFromDistance(filters.distance))

  const distance = useMemo(() => {
    switch (sliderStep) {
      case 0:
        return 5000
      case 1:
        return 10000
      case 2:
        return 20000
      case 3:
        return 30000
      case 4:
        return 50000
      default:
        return 30000
    }
  }, [sliderStep])

  const { data: retailers, error } = useGetNearbyRetailersQuery(
    {
      // @ts-expect-error: skip
      latitude: location?.latitude,
      // @ts-expect-error: skip
      longitude: location?.longitude,
      distance,
    },
    { skip: !location },
  )

  if (error) {
    throw new Error(JSON.stringify(error))
  }

  const styles = useStyles(
    ({ colors, roundness }) => ({
      contentContainer: {
        marginLeft: SpacingBase * 2,
        backgroundColor: colors.background,
        borderRadius: roundness * 3,
        width: windowWidth - SpacingBase * 4,
        height: windowHeight - insets.top - SpacingBase * 4,
        justifyContent: 'flex-start',
      },
      content: {
        padding: SpacingBase * 2,
        flex: 1,
      },
      header: {
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'space-between',
      },
      title: {
        marginLeft: 'auto',
        marginRight: 'auto',
        paddingLeft: SpacingBase * 6,
      },
      distanceContainer: {
        marginBottom: SpacingBase * 4,
      },
      retailersContainer: {
        flex: 1,
        marginBottom: SpacingBase * 2,
      },
      buttonsContainer: {
        flexDirection: 'row',
        marginTop: 'auto',
        marginLeft: -SpacingBase * 2,
      },
      button: {
        flex: 1,
        marginLeft: SpacingBase * 2,
      },
      retailerChipsContainer: {
        flexDirection: 'row',
        flexWrap: 'wrap',
      },
      retailer: {
        marginBottom: SpacingBase,
        marginRight: SpacingBase,
      },
      subtitle: {
        marginBottom: SpacingBase * 2,
      },
      distanceLabel: {
        textAlign: 'center',
        marginTop: SpacingBase * 2,
      },
    }),
    [insets.top, windowHeight, windowWidth],
  )

  useEffect(() => {
    setFilteredRetailers(filters.retailer_ids)
    setSliderStep(getStepFromDistance(filters.distance))
  }, [filters.distance, filters.retailer_ids, getStepFromDistance])

  const handleOnSliderChange = useCallback((value: number) => {
    setSliderStep(value)
  }, [])

  const handleOnRetailerPress = useCallback(
    (id: RetailerFilter['id'], selected: boolean) => {
      if (selected) {
        const newRetailers = filteredRetailers.filter((retailerId) => id !== retailerId)
        setFilteredRetailers(newRetailers)
      } else {
        setFilteredRetailers([...filteredRetailers, id])
      }
    },
    [filteredRetailers],
  )

  const renderRetailerItem = useCallback(
    (r: Retailer) => {
      const selected = filteredRetailers.some((id) => r.id === id)
      return (
        <View key={r.id} style={styles.retailer}>
          <RetailersFilterItem
            retailer={{ id: r.id!, slug: r.slug!, name: r.name! }}
            selected={selected}
            onPress={() => handleOnRetailerPress(r.id!, selected)}
            compact={false}
            showSelectedCheck={false}
          />
        </View>
      )
    },
    [filteredRetailers, handleOnRetailerPress, styles.retailer],
  )

  const handleOnApply = useCallback(() => {
    if (filteredRetailers.length === 0 && distance === FiltersDefault.distance) {
      onDelete()
    } else {
      onApply({ retailer_ids: filteredRetailers, distance })
    }
    onDismiss()
  }, [filteredRetailers, distance, onDismiss, onDelete, onApply])

  const handleOnDismiss = useCallback(() => {
    onDismiss()
  }, [onDismiss])

  const handleOnDelete = useCallback(() => {
    onDelete()
    onDismiss()
  }, [onDelete, onDismiss])

  return (
    <Portal>
      <Modal
        visible={visible}
        onDismiss={handleOnDismiss}
        contentContainerStyle={styles.contentContainer}
      >
        <View style={styles.header}>
          <Text style={styles.title} variant="titleMedium">
            {Copies.filter_offers}
          </Text>
          <IconButton onPress={handleOnDismiss} icon="close" />
        </View>
        <View style={styles.content}>
          <View style={styles.distanceContainer}>
            <Text variant="bodyLarge" style={styles.subtitle}>
              {Copies.by_distance}
            </Text>
            <Slider
              onValueChange={handleOnSliderChange}
              minimumValue={0}
              maximumValue={4}
              step={1}
              value={sliderStep}
              minimumTrackTintColor={colors.secondaryContainer}
              maximumTrackTintColor={colors.secondaryContainer}
              thumbTintColor={colors.secondary}
            />
            <Text variant="labelLarge" style={styles.distanceLabel}>
              {distance / 1000} Km
            </Text>
          </View>
          <View style={styles.retailersContainer}>
            <Text style={styles.subtitle} variant="bodyLarge">
              {Copies.by_retailer}
            </Text>
            <ScrollView contentContainerStyle={styles.retailerChipsContainer}>
              {retailers?.map((r) => renderRetailerItem(r))}
            </ScrollView>
          </View>
          <View style={styles.buttonsContainer}>
            <Button
              icon="delete-outline"
              style={styles.button}
              mode="outlined"
              onPress={handleOnDelete}
            >
              {Copies.reset_filters}
            </Button>
            <Button
              style={styles.button}
              buttonColor={colors.primaryContainer}
              textColor={colors.onPrimaryContainer}
              mode="contained"
              onPress={handleOnApply}
              icon="check"
            >
              {Copies.filter}
            </Button>
          </View>
        </View>
      </Modal>
    </Portal>
  )
}

export default FiltersModal
