import { Chip } from 'react-native-paper'

import type { RetailerFilter } from '../../api/types'
import { useStyles } from '../../utils'

export interface RetailersFilterItemProps {
  retailer: RetailerFilter
  selected: boolean
  onPress: () => void
  compact?: boolean
  showSelectedCheck?: boolean
}

export const RetailersFilterItem = ({
  onPress,
  retailer,
  selected,
  compact = true,
  showSelectedCheck = true,
}: RetailersFilterItemProps) => {
  const styles = useStyles(
    ({ colors }) => ({
      main: {
        backgroundColor: selected ? colors.secondaryContainer : colors.surfaceVariant,
      },
      text: {
        color: selected ? colors.onSecondaryContainer : colors.onSurface,
      },
    }),
    [selected],
  )

  return (
    <Chip
      showSelectedCheck={showSelectedCheck}
      compact={compact}
      selected={selected}
      style={styles.main}
      textStyle={styles.text}
      onPress={onPress}
    >
      {retailer.name}
    </Chip>
  )
}

export default RetailersFilterItem
