import { Image } from 'expo-image'
import { openSettings } from 'expo-linking'
import { requestForegroundPermissionsAsync, PermissionStatus } from 'expo-location'
import { useCallback, useState } from 'react'
import { Platform, View, useWindowDimensions } from 'react-native'
import { Button, IconButton, Modal, Portal, Text, useTheme } from 'react-native-paper'

import { nominatimApi } from '../../api'
import type { NomReverseResultAddress } from '../../api/types'
import { Copies, SpacingBase } from '../../constants'
import { setLocation, useAppDispatch, useAppSelector } from '../../redux'
import { getLocation, logEvent, useStyles } from '../../utils'

export interface GeoLocationButtonProps {
  onLocationRetrieved?: () => void
  onLocationRetrievedFailed?: () => void
}

export const GeoLocationButton = ({
  onLocationRetrieved,
  onLocationRetrievedFailed,
}: GeoLocationButtonProps) => {
  const { colors } = useTheme()
  const dispatch = useAppDispatch()
  const [isLoadingLocation, setIsLoadingLocation] = useState(false)
  const [modalVisible, setModalVisible] = useState(false)
  const { width: windowWidth } = useWindowDimensions()
  const countryCode = useAppSelector((state) => state.app.countryCode)

  const styles = useStyles(
    ({ colors, roundness }) => ({
      modalContentContainer: {
        backgroundColor: colors.background,
        margin: SpacingBase * 2,
        borderRadius: roundness * 3,
        overflow: 'hidden',
      },
      modalHeader: {
        alignItems: 'flex-end',
      },
      modalContent: {
        padding: SpacingBase * 2,
      },
      modalImage: {
        width: windowWidth - SpacingBase * 12,
        height: (windowWidth - SpacingBase * 12) * 0.75,
        alignSelf: 'center',
      },
      modalText: {
        textAlign: 'center',
        marginTop: SpacingBase * 4,
      },
      modalButton: {
        marginTop: SpacingBase * 4,
      },
    }),
    [windowWidth],
  )

  const handleSetLocationDisplay = useCallback((data: NomReverseResultAddress) => {
    const { road, quarter, city, town, village, state } = data
    let firstPart = ''
    let secondPart = ''
    if (road) {
      firstPart = `${road}, `
    } else if (quarter) {
      firstPart = `${quarter}, `
    }
    if (city) {
      secondPart = city
    } else if (town) {
      secondPart = town
    } else if (village) {
      secondPart = village
    } else if (state) {
      secondPart = state
    }
    return firstPart + secondPart
  }, [])

  const handleOnRetrieveLocationPress = useCallback(async () => {
    setIsLoadingLocation(true)

    const { status } = await requestForegroundPermissionsAsync()
    if (status !== PermissionStatus.GRANTED) {
      await logEvent('location_permission_denied', { countryCode })
      setIsLoadingLocation(false)
      if (Platform.OS !== 'web') {
        return setModalVisible(true)
      }
      return
    }

    await logEvent('location_permission_granted', { countryCode })

    let latitude: number | null = null
    let longitude: number | null = null

    await getLocation()
      .then(({ coords }) => {
        latitude = coords.latitude
        longitude = coords.longitude
      })
      .catch(async () => {
        await logEvent('location_retrieved_failed', {
          countryCode,
        })
        onLocationRetrievedFailed && onLocationRetrievedFailed()
        return setIsLoadingLocation(false)
      })

    if (!latitude || !longitude) {
      return
    }

    await logEvent('location_retrieved_success', { countryCode })

    dispatch(nominatimApi.endpoints.nomReverse.initiate({ latitude, longitude }))
      .then(({ data }) => {
        if (!latitude || !longitude) {
          return
        }
        void dispatch(
          setLocation({
            latitude,
            longitude,
            display: data ? handleSetLocationDisplay(data) : `${latitude}, ${longitude}`,
          }),
        )
        setIsLoadingLocation(false)
        onLocationRetrieved && onLocationRetrieved()
      })
      .catch(() => {
        setIsLoadingLocation(false)
        onLocationRetrieved && onLocationRetrieved()
      })
  }, [
    countryCode,
    dispatch,
    handleSetLocationDisplay,
    onLocationRetrieved,
    onLocationRetrievedFailed,
  ])

  const handleOnModalButtonPress = useCallback(async () => {
    if (Platform.OS !== 'web') {
      await openSettings()
    }
    return setModalVisible(false)
  }, [])

  return (
    <>
      <Portal>
        <Modal
          visible={modalVisible}
          onDismiss={() => setModalVisible(false)}
          contentContainerStyle={styles.modalContentContainer}
        >
          <View style={styles.modalHeader}>
            <IconButton onPress={() => setModalVisible(false)} icon="close" />
          </View>
          <View style={styles.modalContent}>
            {/* eslint-disable-next-line @typescript-eslint/no-unsafe-assignment */}
            <Image source={require('../../assets/onboarding/3.png')} style={styles.modalImage} />
            <Text style={styles.modalText} variant="titleLarge">
              {Copies.cannot_geolocalize}
            </Text>
            <Text style={styles.modalText}>
              {Platform.select({
                native: Copies.give_geolocation_permission,
                web: Copies.give_geolocation_permission_web,
              })}
            </Text>
            <Button
              style={styles.modalButton}
              mode="contained-tonal"
              onPress={handleOnModalButtonPress}
            >
              {Platform.select({
                native: Copies.go_to_settings,
                web: Copies.close,
              })}
            </Button>
          </View>
        </Modal>
      </Portal>
      <Button
        loading={isLoadingLocation}
        disabled={isLoadingLocation}
        onPress={handleOnRetrieveLocationPress}
        icon="map-marker-account"
        mode="elevated"
        buttonColor={colors.primaryContainer}
        textColor={colors.onPrimaryContainer}
      >
        {Copies.use_your_location}
      </Button>
    </>
  )
}

export default GeoLocationButton
