import type { BottomTabScreenProps } from '@react-navigation/bottom-tabs'
import { useCallback, useMemo, useState } from 'react'
import { ErrorBoundary } from 'react-error-boundary'
import type { NativeScrollEvent, NativeSyntheticEvent } from 'react-native'
import { Platform, StyleSheet } from 'react-native'
import { AnimatedFAB, FAB } from 'react-native-paper'

import { useGetNearbyCataloguesByRetailerIdsWithAdsQuery } from '../api'
import CatalogueStoreDialog from '../components/CatalogueStoreDialog'
import { CataloguesGrid } from '../components/CataloguesGrid'
import ErrorFallback from '../components/ErrorFallback'
import FiltersModal from '../components/FiltersModal'
import { GeoAction } from '../components/Geo'
import { SpacingBase, Copies } from '../constants'
import type { HomeTabsParamList } from '../navigation/types'
import { deleteFilters, setFilters, useAppDispatch, useAppSelector } from '../redux'
import { storage, storageKeys } from '../utils'

export type CataloguesScreenProps = BottomTabScreenProps<
  HomeTabsParamList,
  'Catalogues',
  'HomeTabs'
>

const styles = StyleSheet.create({
  button: {
    marginLeft: SpacingBase * 2,
    marginRight: SpacingBase * 2,
    marginBottom: SpacingBase * 2,
  },
  fab: {
    position: 'absolute',
    right: SpacingBase * 2,
    bottom: SpacingBase * 2,
  },
  grid: {
    paddingBottom: SpacingBase * 8,
  },
})

export const CataloguesScreen = ({ route, navigation }: CataloguesScreenProps) => {
  const storedFilters = storage.getString(storageKeys.filters)
  const { location, filters } = useAppSelector((state) => state.user)
  const [isFabExtended, setIsFabExtended] = useState(true)

  const hasRetailerFilter = useMemo(
    () => filters.retailer_ids.length > 0,
    [filters.retailer_ids.length],
  )

  const {
    data: catalogues = [],
    error: cataloguesError,
    refetch,
  } = useGetNearbyCataloguesByRetailerIdsWithAdsQuery(
    {
      // @ts-expect-error: skip
      latitude: location?.latitude,
      // @ts-expect-error: skip
      longitude: location?.longitude,
      distance: filters.distance,
      retailer_ids: filters.retailer_ids,
    },
    { skip: !location },
  )

  const [refreshing, setRefreshing] = useState(false)
  const [modalVisible, setModalVisible] = useState(false)
  const dispatch = useAppDispatch()

  if (cataloguesError) {
    throw new Error(JSON.stringify(cataloguesError))
  }

  const handleOnRefresh = useCallback(async () => {
    setRefreshing(true)
    await refetch()
    setRefreshing(false)
  }, [refetch])

  const handleOnModalDismiss = useCallback(() => {
    setModalVisible(false)
  }, [])

  const handleOnFabPress = useCallback(() => {
    setModalVisible(true)
  }, [])

  const handleOnFiltersChange = useCallback(
    ({ retailer_ids, distance }: { retailer_ids: string[]; distance: number }) => {
      dispatch(setFilters({ distance, retailer_ids }))
    },
    [dispatch],
  )

  const handleOnFiltersDelete = useCallback(() => {
    dispatch(deleteFilters())
  }, [dispatch])

  const handleOnScroll = useCallback(({ nativeEvent }: NativeSyntheticEvent<NativeScrollEvent>) => {
    const currentScrollPosition = Math.floor(nativeEvent?.contentOffset?.y) ?? 0
    setIsFabExtended(currentScrollPosition <= 0)
  }, [])

  const fabLabel = useMemo(() => {
    if (!storedFilters) {
      return Copies.filter
    }
    const distanceLabel = `${filters.distance / 1000} Km`
    const retailersLabel = hasRetailerFilter
      ? `, ${filters.retailer_ids.length} ${
          filters.retailer_ids.length === 1 ? Copies.retailer : Copies.retailers
        }`
      : ''
    return `${distanceLabel}${retailersLabel}`
  }, [filters.distance, filters.retailer_ids.length, hasRetailerFilter, storedFilters])

  return (
    <ErrorBoundary FallbackComponent={ErrorFallback}>
      <CataloguesGrid
        catalogues={catalogues}
        onRefresh={handleOnRefresh}
        refreshing={refreshing}
        ListHeaderComponent={<GeoAction style={styles.button} />}
        onScroll={handleOnScroll}
        contentContainerStyle={styles.grid}
      />
      {Platform.OS === 'web' ? (
        <FAB
          variant="primary"
          onPress={handleOnFabPress}
          style={styles.fab}
          icon="filter-outline"
          label={fabLabel}
        />
      ) : (
        <AnimatedFAB
          variant="primary"
          onPress={handleOnFabPress}
          style={styles.fab}
          icon="filter-outline"
          label={fabLabel}
          extended={isFabExtended}
          animateFrom="right"
          iconMode="dynamic"
        />
      )}
      <FiltersModal
        visible={modalVisible}
        onDismiss={handleOnModalDismiss}
        onApply={handleOnFiltersChange}
        onDelete={handleOnFiltersDelete}
      />
      <CatalogueStoreDialog />
    </ErrorBoundary>
  )
}

export default CataloguesScreen
