import type { StackScreenProps } from '@react-navigation/stack'

import { Copies } from '../constants'
import type { RootStackParamList } from '../navigation/types'

export type ContactsScreenProps = StackScreenProps<RootStackParamList, 'Contacts', 'RootStack'>

export const ContactsScreen = ({ route, navigation }: ContactsScreenProps) => {
  return (
    <iframe
      width="100%"
      height="100%"
      style={{ borderWidth: 0 }}
      src={process.env.EXPO_PUBLIC_CONTACTS_URL}
      title={Copies.contacts}
    />
  )
}

export default ContactsScreen
