import type { BottomSheetProps } from '@gorhom/bottom-sheet'
import type BottomSheet from '@gorhom/bottom-sheet/lib/typescript/components/bottomSheet/BottomSheet'
import { useCallback, useRef } from 'react'
import type { NativeEventSubscription } from 'react-native'
import { BackHandler } from 'react-native'

/**
 * hook that dismisses the bottom sheet on the hardware back button press if it is visible
 * @param bottomSheetRef ref to the bottom sheet which is going to be closed/dismissed on the back press
 */
export const useBottomSheetBackHandler = (bottomSheetRef: React.RefObject<BottomSheet | null>) => {
  const backHandlerSubscriptionRef = useRef<NativeEventSubscription | null>(null)
  const handleSheetPositionChange = useCallback<NonNullable<BottomSheetProps['onChange']>>(
    (index) => {
      const isBottomSheetVisible = index >= 1
      if (isBottomSheetVisible && !backHandlerSubscriptionRef.current) {
        // setup the back handler if the bottom sheet is right in front of the user
        backHandlerSubscriptionRef.current = BackHandler.addEventListener(
          'hardwareBackPress',
          () => {
            bottomSheetRef.current?.collapse()
            return true
          },
        )
      } else if (!isBottomSheetVisible) {
        backHandlerSubscriptionRef.current?.remove()
        backHandlerSubscriptionRef.current = null
      }
    },
    [bottomSheetRef, backHandlerSubscriptionRef],
  )
  return { handleSheetPositionChange }
}

export default useBottomSheetBackHandler
