import { useMemo } from 'react'
import { StyleSheet, View } from 'react-native'
import { Icon, Text } from 'react-native-paper'

import type { Catalogue } from '../api/types'
import { ExtraColors, SpacingBase } from '../constants'

export interface CatalogueExpirationProps {
  expiration: Catalogue['_expiration_label']
  urgencyLevel: Catalogue['_expiration_urgency_level']
}

const styles = StyleSheet.create({
  main: {
    flexDirection: 'row',
    alignItems: 'flex-start',
  },
  text: {
    marginLeft: SpacingBase / 2,
  },
})

const CatalogueExpiration = ({ expiration, urgencyLevel }: CatalogueExpirationProps) => {
  const expirationProps = useMemo(() => {
    switch (urgencyLevel) {
      case -1:
        return { icon: 'eye-outline', color: ExtraColors.forestgreen }
      case 0:
        return { icon: 'calendar-outline', color: ExtraColors.forestgreen }
      case 1:
        return { icon: 'calendar-clock-outline', color: ExtraColors.orange }
      case 2:
        return { icon: 'alarm', color: ExtraColors.crimson }
      case 100:
        return { icon: 'clock-remove-outline', color: ExtraColors.gray }
      default:
        return { icon: 'calendar-outline' }
    }
  }, [urgencyLevel])

  return (
    <View style={styles.main}>
      <Icon color={expirationProps.color} size={SpacingBase * 2} source={expirationProps.icon} />
      <Text style={styles.text} variant="labelMedium">
        {expiration}
      </Text>
    </View>
  )
}

export default CatalogueExpiration
