import { Image, ImageBackground } from 'expo-image'
import { View } from 'react-native'
import { Avatar, Card } from 'react-native-paper'

import type { Store } from '../../api/types'
import { SpacingBase } from '../../constants'
import { useStyles } from '../../utils'
import RetailerDistance from '../RetailerDistance'
import StoreOpening from '../StoreOpening'

export interface StoreCardProps {
  id: Store['id']
  address: Store['address']
  city: Store['city']
  retailer: Store['retailer_name']
  distance: Store['_distance']
  imgSource: Store['retailer_thumb_url']
  isOpen?: Store['_isOpen']
  flat?: boolean
  onPress?: (id: Store['id']) => void
  onLongPress?: (id: Store['id']) => void
}

export const StoreCard = ({
  id,
  address,
  city,
  retailer,
  distance,
  flat,
  imgSource,
  isOpen,
  onPress,
  onLongPress,
}: StoreCardProps) => {
  const styles = useStyles(
    ({ colors, roundness }) => ({
      cardContentStyle: {
        flexDirection: 'row',
      },
      logoContainer: {
        justifyContent: 'center',
        padding: SpacingBase,
        backgroundColor: colors.background,
        borderTopLeftRadius: roundness * 3,
        borderBottomLeftRadius: roundness * 3,
        overflow: 'hidden',
      },
      bgImage: {
        opacity: 0.25,
      },
      imageContainer: {
        width: SpacingBase * 6.5,
        height: SpacingBase * 6.5,
        padding: 2,
        backgroundColor: 'white',
        borderRadius: SpacingBase * 3.25,
        overflow: 'hidden',
        borderWidth: 3,
        borderColor: colors.secondaryContainer,
      },
      image: {
        width: SpacingBase * 6.5 - 10,
        height: SpacingBase * 6.5 - 10,
      },
      avatar: {
        backgroundColor: colors.secondaryContainer,
        color: colors.onSecondaryContainer,
      },
      infoContainer: {
        flex: 1,
        paddingBottom: SpacingBase * 2,
        minHeight: 108,
      },
      content: {
        flexDirection: 'row',
        justifyContent: 'space-between',
        marginTop: 'auto',
      },
    }),
    [],
  )

  return (
    <Card
      elevation={flat ? 0 : 1}
      onPress={() => (onPress ? onPress(id) : null)}
      onLongPress={() => (onLongPress ? onLongPress(id) : null)}
      contentStyle={styles.cardContentStyle}
    >
      <ImageBackground
        style={styles.logoContainer}
        // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
        source={require('../../assets/images/map-mini-pink.png')}
        imageStyle={styles.bgImage}
      >
        {imgSource ? (
          <View style={styles.imageContainer}>
            <Image style={styles.image} contentFit="contain" source={imgSource} />
          </View>
        ) : (
          <Avatar.Icon style={styles.avatar} size={SpacingBase * 5} icon="store" />
        )}
      </ImageBackground>
      <View style={styles.infoContainer}>
        <Card.Title
          title={address}
          titleNumberOfLines={1}
          subtitleNumberOfLines={1}
          subtitle={city}
        />
        <Card.Content style={styles.content}>
          {retailer && <RetailerDistance retailer={retailer} distance={distance} />}
          {isOpen !== undefined && <StoreOpening isOpen={isOpen} />}
        </Card.Content>
      </View>
    </Card>
  )
}

export default StoreCard
