import { Image } from 'expo-image'
import { View } from 'react-native'
import type { CardProps } from 'react-native-paper'
import { Card } from 'react-native-paper'

import type { Catalogue } from '../../api/types'
import { SpacingBase } from '../../constants'
import { useStyles } from '../../utils'
import CatalogueExpiration from '../CatalogueExpiration'
import RetailerDistance from '../RetailerDistance'

export interface CatalogueCardProps {
  title: string
  expiration: string
  retailer: string
  distance: string
  urgencyLevel: Catalogue['_expiration_urgency_level']
  onPress: CardProps['onPress']
  onLongPress: CardProps['onLongPress']
  imgSource: string
}

export const CatalogueCard = ({
  title,
  expiration,
  retailer,
  distance,
  urgencyLevel,
  imgSource,
  onPress,
  onLongPress,
}: CatalogueCardProps) => {
  const styles = useStyles(
    ({ colors, roundness }) => ({
      titleContainer: {
        paddingLeft: SpacingBase,
        paddingTop: SpacingBase,
      },
      title: {
        paddingRight: SpacingBase,
      },
      content: {
        paddingLeft: SpacingBase,
        paddingRight: SpacingBase,
        paddingTop: SpacingBase,
        paddingBottom: SpacingBase,
      },
      cover: {
        width: '100%',
        height: 195,
        backgroundColor: colors.secondaryContainer,
        borderRadius: roundness * 3,
      },
      info: {
        paddingTop: SpacingBase,
        paddingLeft: SpacingBase,
        paddingRight: SpacingBase,
      },
    }),
    [],
  )

  return (
    <Card onPress={onPress} onLongPress={onLongPress}>
      <Image source={imgSource} style={styles.cover} contentFit="contain" />
      <View style={styles.info}>
        {retailer.length > 0 && (
          <RetailerDistance spaceBetween retailer={retailer} distance={distance} />
        )}
      </View>
      <Card.Title
        style={styles.titleContainer}
        title={title}
        titleNumberOfLines={2}
        titleStyle={styles.title}
        titleVariant="bodyMedium"
      />
      <Card.Content style={styles.content}>
        <CatalogueExpiration expiration={expiration} urgencyLevel={urgencyLevel} />
      </Card.Content>
    </Card>
  )
}

export default CatalogueCard
