import { StatusBar } from 'expo-status-bar'
import { useEffect, useState } from 'react'
import { AppState } from 'react-native'
import { PaperProvider, Snackbar } from 'react-native-paper'

import { useIpapiJsonQuery } from './api'
import { NavigationProvider, RootStack } from './navigation'
import {
  loadAndShowConsentFormIfRequired,
  setAppStatus,
  setDefaultLocation,
  setLocation,
  setStoredConsentAndChoices,
  useAppDispatch,
  useAppSelector,
} from './redux'
import { storage, storageKeys, DarkTheme, LightTheme } from './utils'

const AppContainer = () => {
  const dispatch = useAppDispatch()
  const theme = useAppSelector((state) => state.ui.theme)
  const { status: appStatus, countryCode } = useAppSelector((state) => state.app)
  const consent_obtained = useAppSelector((state) => state.consent.consent_obtained)
  const { location, onboard } = useAppSelector((state) => state.user)
  const [consentCheck, setConsentCheck] = useState(false)
  const storedLocation = storage.getString(storageKeys.location)
  const {
    data: ipLocation,
    isError,
    error,
  } = useIpapiJsonQuery({}, { skip: !!storedLocation || !!location })
  const [snackVisible, setSnackVisible] = useState(false)

  useEffect(() => {
    if (error && __DEV__) {
      setSnackVisible(true)
    }
  }, [error])

  useEffect(() => {
    if (isError) {
      return void dispatch(setDefaultLocation())
    }

    if (!ipLocation) {
      return
    }

    const { latitude, longitude, city, region, country_code: ipCountryCode } = ipLocation

    if (countryCode !== ipCountryCode.toLowerCase()) {
      return void dispatch(setDefaultLocation())
    }

    if (latitude && longitude) {
      void dispatch(
        setLocation({
          latitude,
          longitude,
          display: city ?? region,
        }),
      )
    }
  }, [countryCode, dispatch, ipLocation, isError])

  useEffect(() => {
    const initConsent = async () => {
      await dispatch(setStoredConsentAndChoices())
    }
    void initConsent().then(() => {
      setConsentCheck(true)
    })
  }, [dispatch])

  useEffect(() => {
    const showConsentForm = async () => {
      await dispatch(loadAndShowConsentFormIfRequired())
    }
    if (consentCheck && !consent_obtained && onboard) {
      void showConsentForm()
    }
  }, [consentCheck, consent_obtained, dispatch, onboard])

  useEffect(() => {
    const appStatusSubscription = AppState.addEventListener('change', (nextAppState) => {
      dispatch(setAppStatus(nextAppState))
    })
    return () => {
      appStatusSubscription.remove()
    }
  }, [appStatus, dispatch])

  return (
    <PaperProvider theme={theme === 'dark' ? DarkTheme : LightTheme}>
      <StatusBar style={theme === 'dark' ? 'light' : 'dark'} />
      <NavigationProvider theme={theme === 'dark' ? DarkTheme : LightTheme}>
        <RootStack />
      </NavigationProvider>
      <Snackbar onDismiss={() => setSnackVisible(false)} visible={snackVisible}>
        {JSON.stringify(error)}
      </Snackbar>
    </PaperProvider>
  )
}

export default AppContainer
