import type { RootStackParamList } from '../../navigation/types'

const createScreenName = (
  routeName: keyof RootStackParamList,
  params: RootStackParamList[keyof RootStackParamList] | object,
) => {
  switch (routeName) {
    case 'CatalogueDetail':
      if ('id' in params) {
        return `${routeName} - ${params.id}`
      }
      break
    case 'StoreDetail':
      if ('id' in params) {
        return `${routeName} - ${params.id}`
      }
      break
    default:
      return routeName
  }
}

export default createScreenName
