import { useLinkProps } from '@react-navigation/native'
import type { StackHeaderProps } from '@react-navigation/stack'
import { Image } from 'expo-image'
import { useCallback, useMemo } from 'react'
import { Platform, StyleSheet, TouchableOpacity } from 'react-native'
import { Appbar, Text } from 'react-native-paper'

import type { CitySlug, CountryCode } from '../navigation/types'
import { useAppSelector } from '../redux'

export interface HeaderProps extends StackHeaderProps {
  children?: React.ReactNode
}

const styles = StyleSheet.create({
  image: {
    height: 40,
    width: 153,
  },
})

const Header = ({ navigation, route, options, children }: HeaderProps) => {
  const isCatalogueDetail = useMemo(() => route.name === 'CatalogueDetail', [route.name])
  const isStoreDetail = useMemo(() => route.name === 'StoreDetail', [route.name])
  const isGeo = useMemo(() => route.name === 'Geo', [route.name])
  const { countryCode, citySlug } = useAppSelector((store) => store.app)
  const { onPress } = useLinkProps({
    to: {
      screen: 'HomeTabs',
      params: {
        screen: 'Catalogues',
        params: citySlug ? { countryCode, citySlug } : { countryCode },
      },
    },
  })

  const showBack = useMemo(
    // show back arrow when land directly to a detail
    () =>
      navigation.canGoBack() ||
      (!navigation.canGoBack() && (isCatalogueDetail || isStoreDetail || isGeo)),
    [isCatalogueDetail, isGeo, isStoreDetail, navigation],
  )

  const handleOnPressBack = useCallback(() => {
    const { countryCode, citySlug } = route.params as {
      countryCode: CountryCode
      citySlug: CitySlug
    }
    navigation.canGoBack()
      ? navigation.goBack()
      : navigation.replace('HomeTabs', {
          screen: 'Catalogues',
          params: citySlug ? { countryCode, citySlug } : { countryCode },
        })
  }, [navigation, route.params])

  return (
    <Appbar.Header mode="center-aligned">
      {showBack && <Appbar.BackAction onPress={handleOnPressBack} />}
      <Appbar.Content
        title={
          Platform.OS !== 'web' && typeof options.headerTitle === 'string' ? (
            <Text variant="bodyLarge">{options.headerTitle}</Text>
          ) : (
            <TouchableOpacity onPress={() => onPress()}>
              <Image
                priority="high"
                style={styles.image}
                contentFit="contain"
                alt={process.env.EXPO_PUBLIC_APP_NAME}
                // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
                source={require('../assets/images/logo-2.png')}
              />
            </TouchableOpacity>
          )
        }
      />
      {children}
    </Appbar.Header>
  )
}

export default Header
