import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'

import type { PhoFeature, PhoFeatureCollection } from './types'
import { DefaultLocations } from '../constants'
import type { CountryCode } from '../navigation/types'
import type { Location } from '../redux/types'

export const photonApi = createApi({
  reducerPath: 'photonApi',
  baseQuery: fetchBaseQuery({ baseUrl: 'https://photon.komoot.io' }),
  endpoints: (builder) => ({
    phoReverse: builder.query<PhoFeature, Location>({
      query: ({ latitude, longitude }) => `reverse?lat=${latitude}&lon=${longitude}`,
      transformResponse: (response: PhoFeatureCollection) => {
        return response.features[0]
      },
    }),
    phoSearch: builder.query<PhoFeature[], { query: string; countryCode: CountryCode }>({
      query: ({ query, countryCode }) => {
        const { boundingBox } = DefaultLocations[countryCode]
        return `/api/?q=${query}&bbox=${boundingBox}`
      },
      transformResponse: (response: PhoFeatureCollection, _, { countryCode }) => {
        const filteredResponse = response.features.filter(
          (feature) => feature.properties.countrycode === countryCode.toUpperCase(),
        )
        return filteredResponse
      },
    }),
  }),
})

export const { usePhoReverseQuery, usePhoSearchQuery } = photonApi
