import { useCallback, useMemo } from 'react'
import type { FlatListProps, ListRenderItem, ListRenderItemInfo } from 'react-native'
import { StyleSheet, FlatList, View, Platform, useWindowDimensions } from 'react-native'

import CataloguesGridPlaceholder from './CataloguesGridPlaceholder'
import type { Catalogue } from '../../api/types'
import { SpacingBase } from '../../constants'
import { useAppSelector } from '../../redux'
import { useBreakpointsValue } from '../../utils'
import { CataloguesListAd } from '../Ads'
import { CataloguesListItem } from '../CataloguesList'

export interface CataloguesGridProps {
  catalogues: Catalogue[]
  ListHeaderComponent?: React.JSX.Element
  onRefresh?: FlatListProps<Catalogue>['onRefresh']
  onScroll?: FlatListProps<Catalogue>['onScroll']
  refreshing?: FlatListProps<Catalogue>['refreshing']
  contentContainerStyle?: FlatListProps<Catalogue>['contentContainerStyle']
}

const styles = StyleSheet.create({
  row: {
    paddingRight: SpacingBase * 2,
    marginBottom: SpacingBase * 2,
  },
  item: {
    flex: 1,
    marginLeft: SpacingBase * 2,
  },
})

export const CataloguesGrid = ({
  catalogues,
  ListHeaderComponent,
  onRefresh,
  refreshing,
  onScroll,
  contentContainerStyle,
}: CataloguesGridProps) => {
  const countryCode = useAppSelector((state) => state.app.countryCode)
  const { width: windowWidth } = useWindowDimensions()

  const numColumns =
    (useBreakpointsValue({ xs: 2, sm: 3, md: 4, lg: 5, xl: 6, xxl: 7 }) as number) || 2

  const maxWidth = useMemo(
    () => windowWidth / numColumns - SpacingBase * 3,
    [numColumns, windowWidth],
  )

  const handleRenderItem: ListRenderItem<Catalogue> = useCallback(
    ({ item, index }: ListRenderItemInfo<Catalogue>) => {
      return (
        <View style={[styles.item, { maxWidth }]}>
          <CataloguesListItem item={item} index={index} countryCode={countryCode} />
        </View>
      )
    },
    [countryCode, maxWidth],
  )

  return (
    <FlatList
      numColumns={numColumns}
      key={numColumns}
      data={catalogues}
      renderItem={handleRenderItem}
      ListHeaderComponent={ListHeaderComponent}
      ListEmptyComponent={
        <CataloguesGridPlaceholder
          items={Array.from({
            length: parseInt(process.env.EXPO_PUBLIC_CATALOGUES_LIST_LIMIT ?? '40', 10),
          })}
        />
      }
      ItemSeparatorComponent={Platform.OS !== 'web' ? CataloguesListAd : undefined}
      columnWrapperStyle={styles.row}
      keyExtractor={(item) => item.id!}
      onRefresh={onRefresh}
      refreshing={refreshing}
      onScroll={onScroll}
      contentContainerStyle={contentContainerStyle}
    />
  )
}

export default CataloguesGrid
