import { useNavigation } from '@react-navigation/native'
import { useCallback } from 'react'
import { StyleSheet } from 'react-native'
import { Button, Text } from 'react-native-paper'

import { Copies, SpacingBase } from '../constants'
import { useAppSelector } from '../redux'

export interface EmptyFavListProps {
  type: 'stores' | 'catalogues'
}

const styles = StyleSheet.create({
  emptyTitle: {
    textAlign: 'center',
    marginBottom: SpacingBase * 4,
  },
})

const EmptyFavList = ({ type }: EmptyFavListProps) => {
  const { countryCode, citySlug } = useAppSelector((state) => state.app)
  const navigation = useNavigation()

  const handleOnEmptyPress = useCallback(() => {
    navigation.navigate('HomeTabs', {
      screen: type === 'catalogues' ? 'Catalogues' : 'Stores',
      params: citySlug ? { citySlug, countryCode } : { countryCode },
    })
  }, [citySlug, countryCode, navigation, type])

  return (
    <>
      <Text style={styles.emptyTitle} variant="bodyLarge">
        {type === 'stores' ? Copies.empty_favourites_stores : Copies.empty_favourites_catalogues}
      </Text>
      <Button onPress={handleOnEmptyPress} mode="elevated">
        {type === 'stores'
          ? Copies.empty_favourites_button_stores
          : Copies.empty_favourites_button_catalogues}
      </Button>
    </>
  )
}

export default EmptyFavList
