import type { BottomTabScreenProps } from '@react-navigation/bottom-tabs'
import { useEffect, useState } from 'react'
import { ErrorBoundary } from 'react-error-boundary'
import { ScrollView, StyleSheet, View } from 'react-native'
import { Divider, List, Snackbar } from 'react-native-paper'
import { useSafeAreaInsets } from 'react-native-safe-area-context'

import { useGetCataloguesByIdsQuery, useGetStoresByIdsQuery } from '../api'
import type { Catalogue, Store } from '../api/types'
import CatalogueStoreDialog from '../components/CatalogueStoreDialog'
import { CataloguesList } from '../components/CataloguesList'
import EmptyFavList from '../components/EmptyFavList'
import ErrorFallback from '../components/ErrorFallback'
import { StoresList } from '../components/StoresList'
import { Copies, SpacingBase } from '../constants'
import type { HomeTabsParamList } from '../navigation/types'
import {
  deleteFavouriteCatalogue,
  deleteFavouriteStore,
  useAppDispatch,
  useAppSelector,
} from '../redux'

export type ForYouScreenProps = BottomTabScreenProps<HomeTabsParamList, 'ForYou', 'HomeTabs'>

const styles = StyleSheet.create({
  listContainer: {
    paddingTop: SpacingBase,
    paddingLeft: SpacingBase * 2,
  },
  placehoderStores: {
    height: 124,
    justifyContent: 'center',
    paddingLeft: SpacingBase * 2,
    paddingRight: SpacingBase * 2,
  },
  placehoderCatalogues: {
    height: 321,
    justifyContent: 'center',
    paddingLeft: SpacingBase * 2,
    paddingRight: SpacingBase * 2,
  },
})

const StoresOrPlaceholder = ({ isLoading, stores }: { isLoading: boolean; stores: Store[] }) => {
  return isLoading || stores.length > 0 ? (
    <StoresList contentContainerStyle={styles.listContainer} stores={stores} />
  ) : (
    <View style={styles.placehoderStores}>
      <EmptyFavList type="stores" />
    </View>
  )
}

const CataloguesOrPlaceholder = ({
  isLoading,
  catalogues,
}: {
  isLoading: boolean
  catalogues: Catalogue[]
}) => {
  return isLoading || catalogues.length > 0 ? (
    <CataloguesList contentContainerStyle={styles.listContainer} catalogues={catalogues} />
  ) : (
    <View style={styles.placehoderCatalogues}>
      <EmptyFavList type="catalogues" />
    </View>
  )
}

export const ForYouScreen = ({ route, navigation }: ForYouScreenProps) => {
  const dispatch = useAppDispatch()
  const [snackbarVisibleStore, setSnackbarVisibleStore] = useState(false)
  const [snackbarVisibleCatalogue, setSnackbarVisibleCatalogue] = useState(false)
  const { favourite_catalogues, favourite_stores } = useAppSelector((state) => state.user)
  const [checkDeleteStore, setCheckDeleteStore] = useState(false)
  const [checkDeleteCatalogue, setCheckDeleteCatalogue] = useState(false)
  const {
    data: catalogues = [],
    error: cataloguesError,
    isLoading: isLoadingCatalogues,
    isSuccess: isSuccessCatalogues,
  } = useGetCataloguesByIdsQuery(favourite_catalogues)
  const {
    data: stores = [],
    error: storesError,
    isLoading: isLoadingStores,
    isSuccess: isSuccessStores,
  } = useGetStoresByIdsQuery(favourite_stores)
  const insets = useSafeAreaInsets()

  // delete not-existing stores
  useEffect(() => {
    if (!isSuccessStores || checkDeleteStore) {
      return
    }
    favourite_stores.forEach((favStore) => {
      if (!stores.some((store) => store.id === favStore)) {
        dispatch(deleteFavouriteStore(favStore))
        setSnackbarVisibleStore(true)
      }
    })
    setCheckDeleteStore(true)
  }, [checkDeleteStore, dispatch, favourite_stores, isSuccessStores, stores])

  // delete not-existing catalogues
  useEffect(() => {
    if (!isSuccessCatalogues || checkDeleteCatalogue) {
      return
    }
    favourite_catalogues.forEach((favCatalogue) => {
      if (!catalogues.some((catalogue) => catalogue.id === favCatalogue)) {
        dispatch(deleteFavouriteCatalogue(favCatalogue))
        setSnackbarVisibleCatalogue(true)
      }
    })
    setCheckDeleteCatalogue(true)
  }, [catalogues, dispatch, favourite_catalogues, isSuccessCatalogues, checkDeleteCatalogue])

  if (cataloguesError || storesError) {
    throw new Error(JSON.stringify(cataloguesError || storesError))
  }

  return (
    <ErrorBoundary FallbackComponent={ErrorFallback}>
      <ScrollView style={{ marginTop: insets.top }}>
        <List.Section title={Copies.your_favourites_stores}>
          <StoresOrPlaceholder isLoading={isLoadingStores} stores={stores} />
        </List.Section>
        <Divider />
        <List.Section title={Copies.your_favourites_catalogues}>
          <CataloguesOrPlaceholder isLoading={isLoadingCatalogues} catalogues={catalogues} />
        </List.Section>
      </ScrollView>
      <CatalogueStoreDialog />
      <Snackbar
        visible={snackbarVisibleStore}
        onDismiss={() => setSnackbarVisibleStore(false)}
        action={{
          label: 'Ok',
          onPress: () => setSnackbarVisibleStore(false),
        }}
      >
        {Copies.force_removed_from_favourites_store}
      </Snackbar>
      <Snackbar
        visible={snackbarVisibleCatalogue}
        onDismiss={() => setSnackbarVisibleCatalogue(false)}
        action={{
          label: 'Ok',
          onPress: () => setSnackbarVisibleCatalogue(false),
        }}
      >
        {Copies.force_removed_from_favourites_catalogue}
      </Snackbar>
    </ErrorBoundary>
  )
}

export default ForYouScreen
