import { createApi, fakeBaseQuery } from '@reduxjs/toolkit/query/react'

import type { Retailer, Catalogue, Store, RetailerFilter } from './types'
import type { Coords } from '../redux/types'
import enrichCatalogue from '../utils/enrichCatalogue'
import enrichStore from '../utils/enrichStore'
import supabaseClient from '../utils/supabaseClient'
import { capitalizeString } from '../utils/text'

const catalogueFields =
  'date_end, date_start, dist_meters, id, pdf_url, thumb_url, title, retailers!inner(*)'

const storeFields =
  'address, city, dist_meters, id, location, name, province, retailer_id, retailer_name, retailer_slug, retailer_thumb_url, website_url, zipcode, latitude, longitude, description, opening_hours, metadata'

export const supabaseApi = createApi({
  reducerPath: 'supabaseApi',
  baseQuery: fakeBaseQuery(),
  endpoints: (builder) => ({
    getNearbyCataloguesByRetailerIdsWithAds: builder.query<
      Catalogue[],
      Coords & { distance: number; retailer_ids: string[] }
    >({
      queryFn: async ({ latitude, longitude, distance, retailer_ids }) => {
        let query = supabaseClient.rpc('nearby_catalogues', {
          distance,
          lat: latitude,
          long: longitude,
        })

        if (retailer_ids.length > 0) {
          query = query.in('retailers.id', retailer_ids)
        }

        const { data, error } = await query
          .select(catalogueFields)
          .limit(parseInt(process.env.EXPO_PUBLIC_CATALOGUES_LIST_LIMIT ?? '30000', 10))

        if (error) {
          return { error }
        }

        const enrichedData = data.map((catalogue, index) => {
          const enrichedCatalogue = enrichCatalogue(catalogue)

          if (index === 1 || (index - 2) % 4 === 3) {
            return { ...enrichedCatalogue, _hasAdNext: true }
          } else {
            return enrichedCatalogue
          }
        })

        return { data: enrichedData }
      },
    }),
    getCataloguesByIds: builder.query<Catalogue[], string[]>({
      queryFn: async (ids) => {
        const { data, error } = await supabaseClient
          .from('catalogues')
          .select(catalogueFields)
          .in('id', ids)
        if (error) {
          return { error }
        }

        const enrichedData = data.map((catalogue) => enrichCatalogue(catalogue))
        return { data: enrichedData }
      },
    }),
    getStoresByIds: builder.query<Store[], string[]>({
      queryFn: async (ids) => {
        const { data, error } = await supabaseClient
          .from('stores')
          .select(storeFields)
          .in('id', ids)
        if (error) {
          return { error }
        }

        const enrichedData = data.map((store) => enrichStore(store))
        return { data: enrichedData }
      },
    }),
    getStoreByIdWithCatalogues: builder.query<Store[], string>({
      queryFn: async (id) => {
        const { data, error } = await supabaseClient
          .from('stores')
          .select(`${storeFields}, catalogues(${catalogueFields})`)
          .eq('id', id)
        if (error) {
          return { error }
        }

        const enrichedData = data.map((store) => enrichStore(store))
        return { data: enrichedData }
      },
    }),
    getNearbyStoresAndRetailersWithAds: builder.query<
      { stores: Store[]; retailers: RetailerFilter[] },
      Coords
    >({
      queryFn: async ({ latitude, longitude }) => {
        const { data, error } = await supabaseClient
          .rpc('nearby_stores', {
            distance: parseInt(process.env.EXPO_PUBLIC_DISTANCE_QUERY ?? '30000', 10),
            lat: latitude,
            long: longitude,
          })
          .select(storeFields)
          .limit(parseInt(process.env.EXPO_PUBLIC_STORES_LIST_LIMIT ?? '50', 10))
        if (error) {
          return { error }
        }

        const enrichedData: {
          stores: Store[]
          retailers: RetailerFilter[]
        } = { stores: [], retailers: [] }

        data.forEach((store, index) => {
          const enrichedStore = enrichStore(store)
          if (index === 1 || (index - 2) % 4 === 3) {
            enrichedData.stores.push({ ...enrichedStore, _hasAdNext: true })
          } else {
            enrichedData.stores.push(enrichedStore)
          }
          if (!store.retailer_id || !store.retailer_name || !store.retailer_slug) {
            return
          }
          const existingRetailer = enrichedData.retailers.find((r) => r.id === store.retailer_id)
          if (!existingRetailer) {
            enrichedData.retailers.push({
              id: store.retailer_id,
              name: capitalizeString(store.retailer_name),
              slug: store.retailer_slug,
            })
          }
        })

        return { data: enrichedData }
      },
    }),
    getNearbyStoresByCatalogueId: builder.query<Store[], Coords & { id: string }>({
      queryFn: async ({ id, latitude, longitude }) => {
        const { data, error } = await supabaseClient
          .rpc('nearby_stores', {
            distance: parseInt(process.env.EXPO_PUBLIC_DISTANCE_QUERY ?? '30000', 10),
            lat: latitude,
            long: longitude,
          })
          .eq('catalogues.id', id)
          .select(`${storeFields}, catalogues!inner(${catalogueFields})`)
        if (error) {
          return { error }
        }

        const enrichedData = data.map((store) => enrichStore(store))
        return { data: enrichedData }
      },
    }),
    getNearbyRetailers: builder.query<Retailer[], Coords & { distance: number }>({
      queryFn: async ({ latitude, longitude, distance }) => {
        const { data, error } = await supabaseClient
          .rpc('nearby_retailers', {
            distance,
            lat: latitude,
            long: longitude,
          })
          .select()

        if (error) {
          return { error }
        }

        const enrichedData = data.map((r) => ({
          ...r,
          name: r.name ? capitalizeString(r.name) : r.name,
        }))

        return { data: enrichedData }
      },
    }),
  }),
})

export const {
  useGetNearbyCataloguesByRetailerIdsWithAdsQuery,
  useGetCataloguesByIdsQuery,
  useGetNearbyStoresByCatalogueIdQuery,
  useGetNearbyStoresAndRetailersWithAdsQuery,
  useGetStoresByIdsQuery,
  useGetStoreByIdWithCataloguesQuery,
  useGetNearbyRetailersQuery,
} = supabaseApi
