import { View } from 'react-native'
import { Text } from 'react-native-paper'

import { SpacingBase } from '../constants'
import { useStyles } from '../utils'

export interface RetailerDistanceProps {
  retailer: string
  distance: string
  spaceBetween?: boolean
}

const RetailerDistance = ({ retailer, distance, spaceBetween }: RetailerDistanceProps) => {
  const styles = useStyles(
    ({ colors, roundness }) => ({
      main: {
        flexDirection: 'row',
        justifyContent: spaceBetween ? 'space-between' : 'flex-start',
        alignItems: 'center',
      },
      retailerContainer: {
        marginRight: SpacingBase,
        backgroundColor: colors.secondaryContainer,
        borderRadius: roundness * 3,
        paddingLeft: SpacingBase / 2,
        paddingRight: SpacingBase / 2,
        paddingTop: SpacingBase / 4,
        paddingBottom: SpacingBase / 4,
        maxWidth: spaceBetween ? '65%' : '100%',
      },
      retailer: {
        color: colors.onSecondaryContainer,
      },
    }),
    [spaceBetween],
  )

  return (
    <View style={styles.main}>
      <View style={styles.retailerContainer}>
        <Text numberOfLines={1} style={styles.retailer} variant="labelSmall">
          {retailer}
        </Text>
      </View>
      <Text variant="labelSmall">{distance}</Text>
    </View>
  )
}

export default RetailerDistance
