import { Fragment } from 'react'
import { StyleSheet, View } from 'react-native'
import { Divider, Text } from 'react-native-paper'

import type { Store } from '../api/types'
import { ExtraColors, SpacingBase } from '../constants'

export interface OpeningHoursProps {
  hours: Store['opening_hours']
}

const styles = StyleSheet.create({
  main: {
    padding: SpacingBase,
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
})

const OpeningHours = ({ hours }: OpeningHoursProps) => {
  return (
    <View>
      {hours.map((hour, index) => {
        const textStyle: object | undefined =
          hour.isToday && hour.isOpen
            ? {
                color: ExtraColors.forestgreen,
                fontWeight: 'bold',
              }
            : hour.isToday && !hour.isOpen
              ? {
                  color: ExtraColors.crimson,
                  fontWeight: 'bold',
                }
              : undefined

        return (
          <Fragment key={hour.dayNumber}>
            <View style={styles.main}>
              <Text style={textStyle}>{hour.day}</Text>
              <View>
                {hour.time.map((time, index) => (
                  <Text style={textStyle} key={index}>
                    {time.from} - {time.to}
                  </Text>
                ))}
              </View>
            </View>
            {index < hours.length - 1 && <Divider />}
          </Fragment>
        )
      })}
    </View>
  )
}

export default OpeningHours
