import { useLinkProps, useNavigation } from '@react-navigation/native'
import { useCallback, useEffect, useMemo } from 'react'
import { StyleSheet } from 'react-native'
import { Button, Dialog, Divider, Icon, List } from 'react-native-paper'

import { Copies, SpacingBase } from '../constants'
import {
  closeCatalogueStoreDialog,
  deleteFavouriteCatalogue,
  deleteFavouriteStore,
  setFavouriteCatalogue,
  setFavouriteStore,
  useAppDispatch,
  useAppSelector,
} from '../redux'

const styles = StyleSheet.create({
  actions: {
    marginTop: SpacingBase * 2,
  },
})

const CatalogueStoreDialog = () => {
  const navigation = useNavigation()
  const countryCode = useAppSelector((state) => state.app.countryCode)
  const { favourite_catalogues, favourite_stores } = useAppSelector((state) => state.user)
  const { open, id, type } = useAppSelector((state) => state.ui.catalogueStoreDialog)
  const isCatalogue = useMemo(() => type === 'catalogue', [type])
  const isFav = useMemo(
    () =>
      id && isCatalogue
        ? favourite_catalogues.includes(id)
        : id && !isCatalogue
          ? favourite_stores.includes(id)
          : false,
    [favourite_catalogues, favourite_stores, id, isCatalogue],
  )
  const dispatch = useAppDispatch()

  const { onPress: onReadPress } = useLinkProps({
    to: {
      screen: isCatalogue ? 'CatalogueDetail' : 'StoreDetail',
      params: { countryCode, id: id ?? '' },
    },
  })

  const handleOnFavPress = useCallback(async () => {
    if (isFav) {
      isCatalogue ? dispatch(deleteFavouriteCatalogue(id!)) : dispatch(deleteFavouriteStore(id!))
    } else {
      isCatalogue
        ? await dispatch(setFavouriteCatalogue(id!))
        : await dispatch(setFavouriteStore(id!))
    }
  }, [dispatch, id, isCatalogue, isFav])

  const handleOnClose = useCallback(() => {
    dispatch(closeCatalogueStoreDialog())
  }, [dispatch])

  useEffect(() => {
    navigation.addListener('state', handleOnClose)
  }, [handleOnClose, navigation])

  return (
    <Dialog visible={open} onDismiss={handleOnClose}>
      <List.Item
        title={isFav ? Copies.remove_from_favourites : Copies.add_to_favoutites}
        onPress={handleOnFavPress}
        right={() => <Icon size={SpacingBase * 3} source={isFav ? 'star' : 'star-outline'} />}
      />
      <Divider />
      <List.Item
        title={isCatalogue ? Copies.read_catalogue : Copies.open_store_detail}
        onPress={onReadPress}
        right={() => (
          <Icon
            size={SpacingBase * 3}
            source={isCatalogue ? 'newspaper-variant-outline' : 'store-outline'}
          />
        )}
      />
      <Dialog.Actions style={styles.actions}>
        <Button onPress={handleOnClose}>{Copies.close}</Button>
      </Dialog.Actions>
    </Dialog>
  )
}

export default CatalogueStoreDialog
