import { useMemo } from 'react'
import { StyleSheet, View } from 'react-native'
import { Icon, Text } from 'react-native-paper'

import { Copies, ExtraColors, SpacingBase } from '../constants'

export interface StoreOpeningProps {
  isOpen: boolean
}

const styles = StyleSheet.create({
  main: {
    flexDirection: 'row',
    alignItems: 'center',
  },
  text: {
    marginLeft: SpacingBase / 2,
  },
})

const StoreOpening = ({ isOpen }: StoreOpeningProps) => {
  const openingProps = useMemo(() => {
    switch (isOpen) {
      case true:
        return { icon: 'clock-check-outline', color: ExtraColors.forestgreen, text: Copies.open }
      default:
        return { icon: 'clock-remove-outline', color: ExtraColors.gray, text: Copies.closed }
    }
  }, [isOpen])

  return (
    <View style={styles.main}>
      <Icon color={openingProps.color} size={SpacingBase * 2} source={openingProps.icon} />
      <Text style={[styles.text, { color: openingProps.color }]} variant="labelMedium">
        {openingProps.text}
      </Text>
    </View>
  )
}

export default StoreOpening
