import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'

import type { NomReverseResult, NomReverseResultAddress } from './types'
import type { Coords } from '../redux/types'

export const nominatimApi = createApi({
  reducerPath: 'nominatimApi',
  baseQuery: fetchBaseQuery({ baseUrl: 'https://nominatim.openstreetmap.org' }),
  endpoints: (builder) => ({
    // https://nominatim.org/release-docs/develop/api/Reverse
    nomReverse: builder.query<NomReverseResultAddress, Coords>({
      query: ({ latitude, longitude }) => `reverse?lat=${latitude}&lon=${longitude}&format=jsonv2`,
      transformResponse: (response: NomReverseResult) => response.address,
    }),
  }),
})

export const { useNomReverseQuery } = nominatimApi
