import { debounce } from 'lodash'
import { useCallback, useMemo, useState } from 'react'
import type { ListRenderItem, ListRenderItemInfo, ViewStyle } from 'react-native'
import { FlatList, StyleSheet, View } from 'react-native'
import { ActivityIndicator, Icon, useTheme } from 'react-native-paper'

import GeoSearchInput from './GeoSearchInput'
import GeoSearchItem from './GeoSearchItem'
import { usePhoSearchQuery } from '../../api'
import type { PhoFeature, PhoFeatureProperties } from '../../api/types'
import { Copies, SpacingBase } from '../../constants'
import { setLocation, useAppDispatch, useAppSelector } from '../../redux'
import { logEvent } from '../../utils'

export interface GeoSearchProps {
  onResultPress?: () => void
  inputContainerStyle?: ViewStyle
}

const styles = StyleSheet.create({
  placeholder: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: SpacingBase * 10,
  },
})

export const GeoSearch = ({ onResultPress, inputContainerStyle }: GeoSearchProps) => {
  const { colors } = useTheme()
  const countryCode = useAppSelector((state) => state.app.countryCode)
  const [query, setQuery] = useState<string>()
  const dispatch = useAppDispatch()
  const { data: searchData = [], isLoading } = usePhoSearchQuery(
    // @ts-expect-error: how to handle query undefined?
    { query, countryCode },
    { skip: !(query && countryCode) },
  )

  const handleDisplayText = (properties: PhoFeatureProperties) => {
    return `${properties.name ?? properties.street}${properties.city ? `, ${properties.city}` : ''}`
  }

  const handleOnChangeText = useMemo(
    () =>
      debounce((query: string) => {
        if (query.length >= 3) {
          setQuery(query)
        }
        if (query.length === 0) {
          setQuery('')
        }
      }, 300),
    [],
  )

  const handleOnPress = useCallback(
    async ({ properties, geometry }: PhoFeature) => {
      await dispatch(
        setLocation({
          latitude: geometry.coordinates[1],
          longitude: geometry.coordinates[0],
          display: handleDisplayText(properties),
        }),
      )
      await logEvent('location_manual_set', { countryCode })
      onResultPress && onResultPress()
    },
    [countryCode, dispatch, onResultPress],
  )

  const handleRenderItem: ListRenderItem<PhoFeature> = useCallback(
    ({ item, index }: ListRenderItemInfo<PhoFeature>) => {
      return (
        <GeoSearchItem
          type={item.properties.type}
          key={index}
          title={handleDisplayText(item.properties)}
          onPress={() => handleOnPress(item)}
        />
      )
    },
    [handleOnPress],
  )

  return (
    <>
      <GeoSearchInput
        containerStyle={inputContainerStyle}
        placeholder={Copies.search_city_or_address}
        onChangeText={handleOnChangeText}
      />
      <FlatList
        initialNumToRender={20}
        ListEmptyComponent={
          <View style={styles.placeholder}>
            {isLoading ? (
              <ActivityIndicator color={colors.secondaryContainer} size="large" />
            ) : (
              <Icon color={colors.outline} source="map-search-outline" size={SpacingBase * 10} />
            )}
          </View>
        }
        data={query && query.length > 0 ? searchData : []}
        renderItem={handleRenderItem}
        keyExtractor={(_, index) => `${index}`}
        keyboardShouldPersistTaps="always"
      />
    </>
  )
}

export default GeoSearch
