import type { BottomSheetBackdropProps } from '@gorhom/bottom-sheet'
import BottomSheetLib, {
  BottomSheetBackdrop,
  BottomSheetHandle,
  BottomSheetView,
} from '@gorhom/bottom-sheet'
import { useCallback, useRef, useState } from 'react'
import { Button, useTheme } from 'react-native-paper'

import { BottomSheetHandleHeight, SpacingBase } from '../../constants'
import { useBottomSheetBackHandler, useStyles } from '../../utils'

export interface BottomSheetProps {
  children: React.ReactNode
  backgroundColor?: string
  hasBackdrop?: boolean
  snapPoints?: (string | number)[]
  index?: number
  handleComponentText?: string
}

export const BottomSheet = ({
  children,
  backgroundColor,
  hasBackdrop,
  snapPoints,
  index,
  handleComponentText,
}: BottomSheetProps) => {
  const bottomSheetRef = useRef<BottomSheetLib>(null)
  const [currentIndex, setCurrentIndex] = useState(index ?? 0)
  const { colors } = useTheme()
  const { handleSheetPositionChange } = useBottomSheetBackHandler(bottomSheetRef)

  const renderBackdrop = useCallback(
    (props: BottomSheetBackdropProps) => (
      <BottomSheetBackdrop
        // eslint-disable-next-line react/jsx-props-no-spreading
        {...props}
        appearsOnIndex={1}
        disappearsOnIndex={0}
        pressBehavior="collapse"
      />
    ),
    [],
  )

  const styles = useStyles(
    () => ({
      main: {
        backgroundColor,
      },
      contentContainer: {},
      customHandle: {
        padding: SpacingBase,
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
      },
    }),
    [backgroundColor],
  )

  const handleOnChange = useCallback(
    (index: number) => {
      setCurrentIndex(index)
      handleSheetPositionChange(index)
    },
    [handleSheetPositionChange],
  )

  const handleOnHandlePress = useCallback(() => {
    currentIndex === 0 ? bottomSheetRef.current?.expand() : bottomSheetRef.current?.collapse()
  }, [currentIndex])

  return (
    <BottomSheetLib
      ref={bottomSheetRef}
      index={index ?? 0}
      snapPoints={snapPoints ?? [BottomSheetHandleHeight, '50%']}
      backgroundStyle={styles.main}
      handleHeight={BottomSheetHandleHeight}
      backdropComponent={hasBackdrop ? renderBackdrop : undefined}
      onChange={handleOnChange}
      handleComponent={
        handleComponentText
          ? () => (
              <Button
                textColor={colors.onSurfaceVariant}
                icon={currentIndex === 0 ? 'chevron-up' : 'chevron-down'}
                onPress={handleOnHandlePress}
                labelStyle={{ marginTop: SpacingBase - 2 }}
              >
                {handleComponentText}
              </Button>
            )
          : BottomSheetHandle
      }
    >
      <BottomSheetView style={styles.contentContainer}>{children}</BottomSheetView>
    </BottomSheetLib>
  )
}

export default BottomSheet
