import type { LocationObject } from 'expo-location'
import { getCurrentPositionAsync, LocationAccuracy } from 'expo-location'
import { Platform } from 'react-native'

const delay = (timeInMilliseconds: number) => {
  return new Promise<null>((resolve) => {
    setTimeout(() => resolve(null), timeInMilliseconds)
  })
}

export const getLocation = async () => {
  const ANDROID_DELAY_IN_MS = 8 * 1000 // 8s
  const IOS_DELAY_IN_MS = 15 * 1000 // 15s

  const DELAY_IN_MS = Platform.OS === 'ios' ? IOS_DELAY_IN_MS : ANDROID_DELAY_IN_MS

  const MAX_TRIES = 3
  let tries = 1

  let location: LocationObject | null = null

  let locationError: Error | null = null

  do {
    try {
      location = await Promise.race([
        delay(DELAY_IN_MS),
        getCurrentPositionAsync({
          accuracy: LocationAccuracy.High,
          distanceInterval: 0,
        }),
      ])

      if (!location) {
        throw new Error('Timeout')
      }
    } catch (err) {
      locationError = err as Error
    } finally {
      tries += 1
    }
  } while (!location && tries <= MAX_TRIES)

  if (!location) {
    throw locationError ?? new Error('Location unavailable')
  }

  return location
}

export default getLocation
