/* eslint-disable camelcase */
import { format, getISODay } from 'date-fns'
// TODO: handle country-based locales
import { it } from 'date-fns/locale'

import enrichCatalogue from './enrichCatalogue'
import { capitalizeString, formatDistanceNumber } from './text'
import type { QueriedStore, Store } from '../api/types'

const formatPeriods = (
  input: {
    open?: { day: number; time: string }
    close?: { day: number; time: string }
  }[],
): Store['opening_hours'] => {
  const today = getISODay(new Date())
  const currentTime = format(new Date(), 'HH:mm')

  const output = input
    .map((entry) => {
      if (!entry.close || !entry.open) {
        return undefined
      }
      const { close, open } = entry
      const currentDate = new Date()
      const dayName = format(
        new Date(
          currentDate.getFullYear(),
          currentDate.getMonth(),
          currentDate.getDate() + (open.day - currentDate.getDay()),
        ),
        'EEEE',
        { locale: it },
      )
      const timeFrom = format(
        new Date(
          currentDate.getFullYear(),
          currentDate.getMonth(),
          currentDate.getDate(),
          parseInt(open.time.slice(0, 2), 10),
          parseInt(open.time.slice(2), 10),
        ),
        'HH:mm',
      )
      const timeTo = format(
        new Date(
          currentDate.getFullYear(),
          currentDate.getMonth(),
          currentDate.getDate(),
          parseInt(close.time.slice(0, 2), 10),
          parseInt(close.time.slice(2), 10),
        ),
        'HH:mm',
      )

      // TODO: remove the 0/7 check when we align to standard iso
      const isToday = open.day === today || (open.day === 0 && today === 7)

      let isOpen: boolean
      if (close.day > open.day || (close.day === open.day && close.time >= open.time)) {
        // Normal case: close.day is the same or greater than open.day
        isOpen = isToday && currentTime >= timeFrom && currentTime <= timeTo
      } else {
        // Special case: close.time is on the next day
        isOpen = isToday && (currentTime >= timeFrom || currentTime <= timeTo)
      }

      return {
        dayNumber: open.day,
        day: dayName,
        time: [{ from: timeFrom, to: timeTo }],
        isToday,
        isOpen,
      }
    })

    .reduce<Store['opening_hours']>((acc, entry) => {
      if (!entry) {
        return acc
      }

      const existingDayEntry = acc.find((item) => item.day === entry.day)

      if (existingDayEntry) {
        existingDayEntry.time.push(...entry.time)
      } else {
        acc.push(entry)
      }

      return acc
    }, [])
    .sort((a, b) => {
      // TODO: remove the 0 check when we align to standard iso
      if (a.dayNumber === 0) {
        return 1
      }
      if (b.dayNumber === 0) {
        return -1
      }
      return a.dayNumber - b.dayNumber
    })

  return output
}

const formatOpeningHours = (
  opening_hours: QueriedStore['opening_hours'],
): Store['opening_hours'] => {
  if (
    !opening_hours ||
    typeof opening_hours !== 'object' ||
    Array.isArray(opening_hours) ||
    !opening_hours.periods ||
    !Array.isArray(opening_hours.periods)
  ) {
    return []
  }
  // TODO
  // @ts-expect-error: spaghetty
  return formatPeriods(opening_hours.periods)
}

export const enrichStore = (s: QueriedStore): Store => {
  const opening_hours = formatOpeningHours(s.opening_hours)

  return {
    ...s,
    city: s.city ? capitalizeString(s.city) : s.city,
    address: s.address ? capitalizeString(s.address) : s.address,
    retailer_name: s.retailer_name ? capitalizeString(s.retailer_name) : s.retailer_name,
    catalogues: s.catalogues ? s.catalogues.map((c) => enrichCatalogue(c)) : undefined,
    opening_hours,
    _isOpen: opening_hours.length > 0 ? opening_hours.some((entry) => entry.isOpen) : undefined,
    _distance: s.dist_meters && s.dist_meters >= 0 ? formatDistanceNumber(s.dist_meters) : '',
  }
}

export default enrichStore
