import { useMemo } from 'react'
import { StyleSheet } from 'react-native'
import { List } from 'react-native-paper'

import type { PhoFeatureProperties } from '../../api/types'
import { SpacingBase } from '../../constants'

export interface GeoSearchItemProps {
  type: PhoFeatureProperties['type']
  title: string
  onPress?: () => void
}

const styles = StyleSheet.create({
  listItem: {
    paddingLeft: SpacingBase * 2,
    paddingRight: SpacingBase * 2,
  },
})

const GeoSearchItem = ({ type, title, onPress }: GeoSearchItemProps) => {
  const icon = useMemo(() => {
    switch (type) {
      case 'street':
        return 'road-variant'
      case 'house':
        return 'office-building-marker'
      case 'county':
        return 'home-city-outline'
      case 'city':
        return 'city-variant-outline'
      default:
        return 'map-marker-outline'
    }
  }, [type])

  return (
    <List.Item
      style={styles.listItem}
      title={title}
      left={() => <List.Icon icon={icon} />}
      onPress={onPress}
    />
  )
}

export default GeoSearchItem
