import { createStackNavigator } from '@react-navigation/stack'
import { useMemo } from 'react'
import { Platform } from 'react-native'

import HomeTabs from './HomeTabs'
import type { RootStackParamList } from './types'
import Header from '../components/Header'
import { Copies } from '../constants'
import { useAppSelector } from '../redux'
import {
  CatalogueDetailScreen,
  ContactsScreen,
  GeoScreen,
  NotFoundScreen,
  OnBoardingScreen,
  PrivacyPolicyScreen,
  StoreDetailScreen,
} from '../screens'

const Stack = createStackNavigator<RootStackParamList>()

export const RootStack = () => {
  const { countryCode, citySlug } = useAppSelector((state) => state.app)
  const onboard = useAppSelector((state) => state.user.onboard)

  // TODO: avoid ternary when bug will be fixed:
  // - https://github.com/react-navigation/react-navigation/issues/8671
  // - https://github.com/react-navigation/react-navigation/issues/7909
  const initialParams = useMemo(
    () => (citySlug ? { countryCode, citySlug } : { countryCode }),
    [citySlug, countryCode],
  )

  return (
    <Stack.Navigator
      id="RootStack"
      screenOptions={{
        headerMode: 'screen',
        // eslint-disable-next-line react/jsx-props-no-spreading
        header: (props) => <Header {...props} />,
      }}
      initialRouteName={Platform.select({
        native: onboard ? 'HomeTabs' : 'Onboarding',
        web: 'HomeTabs',
      })}
    >
      <Stack.Screen name="Onboarding" component={OnBoardingScreen} initialParams={initialParams} />
      <Stack.Screen name="HomeTabs" component={HomeTabs} options={{ headerShown: false }} />
      <Stack.Screen name="CatalogueDetail" component={CatalogueDetailScreen} />
      <Stack.Screen name="StoreDetail" component={StoreDetailScreen} />
      <Stack.Screen
        name="Geo"
        component={GeoScreen}
        options={{ title: `${process.env.EXPO_PUBLIC_APP_NAME} - ${Copies.change_location}` }}
      />
      <Stack.Screen
        name="Contacts"
        component={ContactsScreen}
        options={{ title: `${process.env.EXPO_PUBLIC_APP_NAME} - ${Copies.contacts}` }}
      />
      <Stack.Screen name="NotFound" component={NotFoundScreen} />
      <Stack.Screen name="PrivacyPolicy" component={PrivacyPolicyScreen} />
    </Stack.Navigator>
  )
}

export default RootStack
