import 'react-native-url-polyfill/auto'
import type { Database } from '@milloome/types'
import { createClient } from '@supabase/supabase-js'

const supabaseUrl = process.env.EXPO_PUBLIC_SUPABASE_URL ?? false
const supabaseAnonKey = process.env.EXPO_PUBLIC_SUPABASE_ANON_KEY ?? false

if (!supabaseUrl || !supabaseAnonKey) {
  throw new Error('supabase connection error')
}

export const supabaseClient = createClient<Database>(supabaseUrl, supabaseAnonKey)

export default supabaseClient
