import type { StackScreenProps } from '@react-navigation/stack'
import { Image } from 'expo-image'
import { useCallback, useEffect, useMemo, useState } from 'react'
import { ScrollView, View, useWindowDimensions } from 'react-native'
import { createOpenLink } from 'react-native-open-maps'
import { ActivityIndicator, Appbar, Avatar, FAB, Surface, Text, useTheme } from 'react-native-paper'

import { useGetStoreByIdWithCataloguesQuery } from '../api'
import type { Store } from '../api/types'
import { StoreDetailAd } from '../components/Ads'
import { CataloguesList } from '../components/CataloguesList'
import type { HeaderProps } from '../components/Header'
import Header from '../components/Header'
import OpeningHours from '../components/OpeningHours'
import StoreOpening from '../components/StoreOpening'
import { StoreStaticMap } from '../components/StoresMap'
import { Copies, ExtraColors, SpacingBase } from '../constants'
import type { RootStackParamList } from '../navigation/types'
import { deleteFavouriteStore, setFavouriteStore, useAppDispatch, useAppSelector } from '../redux'
import { useStyles } from '../utils'

export type StoreDetailScreenProps = StackScreenProps<
  RootStackParamList,
  'StoreDetail',
  'RootStack'
>

export const StoreDetailScreen = ({ route, navigation }: StoreDetailScreenProps) => {
  const { id } = route.params
  const [store, setStore] = useState<Store | null>(null)
  const favourite_stores = useAppSelector((state) => state.user.favourite_stores)
  const { data: stores, error, isLoading } = useGetStoreByIdWithCataloguesQuery(id)
  const { height: windowHeight } = useWindowDimensions()
  const isFav = useMemo(
    () => (store?.id ? favourite_stores.includes(store.id) : false),
    [store, favourite_stores],
  )
  const dispatch = useAppDispatch()
  const { colors } = useTheme()

  const styles = useStyles(
    ({ colors, roundness }) => ({
      loaderContainer: {
        flex: 1,
        justifyContent: 'center',
      },
      mapContainer: {
        height: windowHeight / 5,
        borderBottomWidth: SpacingBase / 2,
        borderColor: colors.secondaryContainer,
      },
      directionsButton: {
        position: 'absolute',
        bottom: SpacingBase * 2,
        right: SpacingBase * 2,
        backgroundColor: colors.secondaryContainer,
      },
      logoContainer: {
        width: SpacingBase * 10,
        height: SpacingBase * 10,
        borderRadius: SpacingBase * 5,
        borderWidth: SpacingBase / 2,
        borderColor: colors.secondaryContainer,
        padding: SpacingBase / 2,
        backgroundColor: 'white',
        overflow: 'hidden',
        alignSelf: 'center',
        marginTop: -SpacingBase * 5,
      },
      logo: {
        width: SpacingBase * 8,
        height: SpacingBase * 8,
      },
      avatar: {
        backgroundColor: colors.secondaryContainer,
      },
      detailsContainer: {
        paddingLeft: SpacingBase * 2,
        paddingRight: SpacingBase * 2,
      },
      opening: {
        marginTop: SpacingBase,
        alignItems: 'center',
      },
      title: {
        marginTop: SpacingBase,
        textAlign: 'center',
      },
      subtitle: {
        textAlign: 'center',
        marginTop: SpacingBase,
      },
      hoursContainer: {
        marginTop: SpacingBase * 2,
      },
      cataloguesContainer: {
        marginTop: SpacingBase * 2,
        paddingLeft: SpacingBase * 2,
        paddingTop: SpacingBase * 2,
      },
      emptyContainer: {
        margin: SpacingBase * 2,
        padding: SpacingBase * 2,
        borderRadius: roundness * 3,
        backgroundColor: colors.secondaryContainer,
      },
      emptyTitle: {
        marginBottom: SpacingBase * 2,
        textAlign: 'center',
      },
      descriptionContainer: {
        padding: SpacingBase * 2,
      },
    }),
    [windowHeight],
  )

  if (error) {
    throw new Error(JSON.stringify(error))
  }

  const handleOnPressFav = useCallback(async () => {
    if (!store) {
      return
    }
    if (isFav) {
      dispatch(deleteFavouriteStore(store.id!))
    } else {
      await dispatch(setFavouriteStore(store.id!))
    }
  }, [store, dispatch, isFav])

  const renderHeader = useCallback(
    (props: HeaderProps) => {
      return (
        // eslint-disable-next-line react/jsx-props-no-spreading
        <Header {...props}>
          <Appbar.Action
            size={SpacingBase * 4}
            onPress={handleOnPressFav}
            color={ExtraColors.gold}
            icon={isFav ? 'star' : 'star-outline'}
          />
        </Header>
      )
    },
    [handleOnPressFav, isFav],
  )

  useEffect(() => {
    if (stores && stores.length > 0) {
      setStore(stores[0])
      navigation.setOptions({
        header: renderHeader,
        headerTitle: stores[0].retailer_name!,
        title: `${process.env.EXPO_PUBLIC_APP_NAME} - ${Copies.store} ${stores[0].retailer_name}, ${stores[0].address}, ${stores[0].city}`,
      })
    }
  }, [navigation, renderHeader, stores])

  return isLoading || !store ? (
    <View style={styles.loaderContainer}>
      <ActivityIndicator color={colors.secondaryContainer} size="large" />
    </View>
  ) : (
    <ScrollView>
      <View style={styles.mapContainer}>
        <StoreStaticMap store={store} />
        <FAB
          onPress={createOpenLink({
            query: `${store.name}, ${store.address}`,
            queryPlaceId: store.metadata?.google_place_id,
          })}
          icon="directions"
          size="small"
          style={styles.directionsButton}
          color={colors.onSecondaryContainer}
        />
      </View>
      <View style={styles.logoContainer}>
        {store.retailer_thumb_url ? (
          <Image contentFit="contain" style={styles.logo} source={store.retailer_thumb_url} />
        ) : (
          <Avatar.Icon
            color={colors.onSecondaryContainer}
            style={styles.avatar}
            size={SpacingBase * 8}
            icon="store"
          />
        )}
      </View>
      <View style={styles.detailsContainer}>
        {store._isOpen !== undefined && (
          <View style={styles.opening}>
            <StoreOpening isOpen={store._isOpen} />
          </View>
        )}
        <Text style={styles.title} variant="headlineSmall">
          {store.address}
        </Text>
        <Text style={styles.subtitle}>{store.city}</Text>
        {store.opening_hours.length > 0 && (
          <View style={styles.hoursContainer}>
            <OpeningHours hours={store.opening_hours} />
          </View>
        )}
      </View>
      {store.catalogues && store.catalogues.length > 0 ? (
        <>
          <StoreDetailAd />
          <CataloguesList
            contentContainerStyle={styles.cataloguesContainer}
            catalogues={store.catalogues}
          />
        </>
      ) : (
        <Surface style={styles.emptyContainer}>
          <Text style={styles.emptyTitle} variant="bodyLarge">
            {Copies.no_offers_for_store_title}
          </Text>
          <Text>{Copies.no_offers_for_store_subtitle}</Text>
        </Surface>
      )}
      {store.description && (
        <View style={styles.descriptionContainer}>
          <Text variant="bodyLarge">{store.description}</Text>
        </View>
      )}
    </ScrollView>
  )
}

export default StoreDetailScreen
