import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'

import type { IpApiResponse } from './types'

export const ipapiApi = createApi({
  reducerPath: 'ipapiApi',
  baseQuery: fetchBaseQuery({ baseUrl: 'https://ipapi.co' }),
  endpoints: (builder) => ({
    ipapiJson: builder.query<IpApiResponse, unknown>({
      query: () => `json`,
    }),
  }),
})

export const { useIpapiJsonQuery } = ipapiApi
